//Helper function to format Date into API Call correct data
const formatDate = (date) => {
  const d = new Date(date);
  let month = '' + (d.getMonth() + 1);
  let day = '' + d.getDate();
  const year = d.getFullYear();

  if (month.length < 2) month = '0' + month;
  if (day.length < 2) day = '0' + day;

  return [year, month, day].join('-');
};

//API Call for specific vesselTrack of one currentTrackedVessel
async function vesselTrack(startDate, endDate) {
  if (!currentTrackedVessel.value) return;
  
  const vesselTrackingUrl = 'https://sdcs-api.dev.peasec.de/fishing/v3/events';
  const extendedStartDate = new Date(startDate);
  const extendedEndDate = new Date(endDate);
  extendedStartDate.setMonth(extendedStartDate.getMonth() - 5);
  extendedEndDate.setMonth(extendedEndDate.getMonth() + 5);
  
  const baseParams = {
    limit: 1000,
    offset: 0,
    sort: '-start',
    vessels: [currentTrackedVessel.value],
    'start-date': formatDate(extendedStartDate),
    'end-date': formatDate(extendedEndDate),
  };
  
  try {
    const [portVisitsResponse, loiteringResponse] = await Promise.all([
      axios.get(vesselTrackingUrl, {
        params: {...baseParams, 'datasets[0]': 'public-global-port-visits-c2-events:latest'}
      }),
      axios.get(vesselTrackingUrl, {
        params: {...baseParams, 'datasets[0]': 'public-global-loitering-events:latest'}
      })
    ]);
    
    // Check if the responses have the expected structure
    const portVisits = portVisitsResponse?.data?.entries || [];
    const loiteringEvents = loiteringResponse?.data?.entries || [];
    
    // Combine and sort the events from both responses
    const allEvents = [
      ...portVisits.map(entry => ({...entry, type: 'PortVisit'})),
      ...loiteringEvents.map(entry => ({...entry, type: 'Loitering'}))
    ].sort((a, b) => new Date(b.start) - new Date(a.start));
    
    // Check if we have any events
    if (allEvents.length === 0) {
      console.warn("No vessel events found for the selected time period");
      interpolatedEvents.value = [];
      return;
    }
    
    // Process the events
    const formattedEvents = allEvents.map(entry => {
      // Validate each entry has the required fields
      if (!entry.position || !entry.vessel) {
        console.warn("Invalid event entry:", entry);
        return null;
      }
      
      return {
        start: entry.start,
        end: entry.end,
        position: entry.position,
        flag: entry.vessel?.flag || 'Unknown',
        name: entry.vessel?.name || 'Unknown Vessel',
        mmsi: entry.vessel?.ssvid || 'Unknown',
        type: entry.type
      };
    }).filter(event => event !== null); // Remove any null entries
    
    interpolatedEvents.value = formattedEvents;
    
    if (formattedEvents.length > 0) {
      drawVesselTrack(interpolatedEvents.value);
    } else {
      console.warn("No valid vessel events found after filtering");
    }
  } catch (error) {
    console.error("Error fetching vessel track data:", error);
    interpolatedEvents.value = [];
  }
}
//Function to draw the vessel Track on the mapInstance
const drawVesselTrack = (trackData) => {

  
  // Get map instance, with fallback options
  let mapInstanceToUse = props.mapInstance;
  
  if (!mapInstanceToUse && props.cableStore) {
    mapInstanceToUse = props.cableStore.getMapInstance;
  }
  
  if (!trackData || trackData.length === 0) {
    console.warn("No track data available");
    return;
  }
  
  const startDate = new Date(formattedStartDate.value);
  const endDate = new Date(formattedEndDate.value);
  

  const timeDiff = endDate - startDate;
  if (timeDiff < 3600000) { // Less than 1 hour
    console.warn("Time range is too short, which may result in insufficient data");
  }

  if (isNaN(startDate) || isNaN(endDate)) {
    console.error("Invalid date range provided:", formattedStartDate.value, formattedEndDate.value);
    return;
  }
  
  // Filter the track data within the date range
  const filteredData = trackData.filter(event => {
    const eventStartDate = new Date(event.start);
    const eventEndDate = new Date(event.end);
    return (
      (eventStartDate >= startDate && eventStartDate <= endDate) ||
      (eventEndDate >= startDate && eventEndDate <= endDate)
    );
  });
    
  if (filteredData.length === 0) {
    console.warn("No track data within the specified date range.");
    return;
  }
  
  // Remove existing vessel track layer if it exists
  if (vesselTrackLayer.value && mapInstanceToUse.hasLayer(vesselTrackLayer.value)) {
    mapInstanceToUse.removeLayer(vesselTrackLayer.value);
  }
  
  // Get the track points
  const trackPoints = filteredData
    .map(event => {
      const lat = event.position?.lat;
      const lon = event.position?.lon;
      
      if (!isValidCoordinate(lat, lon)) {
        console.warn("Invalid coordinates in track data", event);
        return null;
      }
      
      return [lat, lon];
    })
    .filter(point => point !== null)
    .reverse();

  if (trackPoints.length < 2) {
    console.warn("Not enough valid track points to draw polyline");
    return;
  }

  try {
    if (vesselTrackLayer.value && mapInstanceToUse.hasLayer(vesselTrackLayer.value)) {
      mapInstanceToUse.removeLayer(vesselTrackLayer.value);
    }
    vesselTrackLayer.value = L.polyline(trackPoints, {
      color: '#ad7dad',
      weight: 3,
      opacity: 0.7
    }).arrowheads({
      size: '20px',
      yawn: 30,
      fill: true
    }).addTo(mapInstanceToUse);
    
    // Fit the map to the bounds of the track
    if (vesselTrackLayer.value) {
      vesselTrackLayer.value.addTo(mapInstanceToUse);
      
      // Only fit bounds if there are valid points
      if (vesselTrackLayer.value.getBounds && !vesselTrackLayer.value.getBounds().isValid()) {
        console.warn("Invalid bounds for vessel track layer");
      } else if (vesselTrackLayer.value.getBounds) {
        mapInstanceToUse.fitBounds(vesselTrackLayer.value.getBounds());
      }
    }
      } catch (error) {
    console.error("Error creating polyline:", error);
  }
};

