      }

      // Fetch vessel track data
      await vesselTrack(formattedStartDate.value, formattedEndDate.value);
      toggleVesselTracking();
      //emit('update:viewingVesselTrack', true);

      // Explicitly update cable opacity using the store
      cableStore.setAllCablesOpacity(0.15); // Ensure this is called

      // Force a reactivity update
      await nextTick();
    } else {
      console.error('Cable store or dataCables not properly initialized');
    }
  } catch (error) {
    console.error('Error in viewVesselTrack:', error);
    isVesselTrackingMode.value = false;
    vesselTrackingSelected.value = false;
    isSearchSelected.value = false;
  }
};

const exitVesselTrackingMode = () => {
  isVesselTrackingMode.value = false;
  vesselTrackingSelected.value = false;
  isSearchSelected.value = false;
  currentTrackedVessel.value = null;
  hoveredMarker.value = null;
  emit( 'update:hoveredMarker', null);
  
  // Clear the cable ships layer group
  const updatedPositions = props.vesselPositions.filter(vessel => {
    if (vessel.fromSidebar) {
      // Remove sidebar-created markers
      if (vessel.marker && props.mapInstance.hasLayer(vessel.marker)) {
        props.mapInstance.removeLayer(vessel.marker);
       }
      if (vessel.popup && props.mapInstance.hasLayer(vessel.popup)) {
        props.mapInstance.removeLayer(vessel.popup);        
      }        
    }
    props.layerGroup.clearLayers();
  });

  toggleVesselTracking();
  //emit('update:viewingVesselTrack', false);
  
  vesselTracking();
  props.cableStore.dataCables.forEach(cable => {
    cable.opacity = 1;
  });
  
  if (vesselTrackLayer.value) {
    props.mapInstance.removeLayer(vesselTrackLayer.value);
    vesselTrackLayer.value = null;
  }
};

const viewingVesselTrack = ref(false);

const toggleVesselTracking = () => {
  const newValue = !viewingVesselTrack.value;
  viewingVesselTrack.value = newValue;
  cableStore.setViewingVesselTrack(newValue);
};

//Handle Ship Clicks
const onShipClick = (e, vessel) => {
  try {    
    if (!vessel || !vessel.lastSeen) {
      console.error('Invalid vessel data provided to onShipClick');
      return;
    }

    // Get map instance from the cable store
    const mapInstance = props.cableStore.getMapInstance;
    if (!mapInstance) {
      console.error('Map instance is not available from cableStore');
      return;
    }

    const lastSeenDate = new Date(vessel.lastSeen);
    const formattedDate = lastSeenDate.toLocaleString('en-GB', {
      year: 'numeric',
      month: 'numeric',
      day: 'numeric',
      hour: 'numeric',
      minute: 'numeric',
      second: 'numeric',
      hour12: false
    });

    // Create popup content
    const popupContent = `
      <div>
        <strong>${vessel.name || 'Unknown Vessel'}</strong><br/>
        <strong>${currentTranslations.value?.lastSeen || 'Last Seen'}:</strong> ${formattedDate}<br/>
        <strong>${currentTranslations.value?.event || 'Event'}:</strong> ${vessel.eventType || 'Unknown'}<br/>
        <strong>MMSI:</strong> ${vessel.ssvid || 'Unknown'}<br/>
        <strong>${currentTranslations.value?.flag || 'Flag'}:</strong> ${getFlagEmoji(vessel.flag)}${vessel.flag}<br/>
        <strong>${currentTranslations.value?.length || 'Length'}:</strong> ${vessel.length || 'Unknown'}<br/>
        <strong>${currentTranslations.value?.beam || 'Beam'}:</strong> ${vessel.beam || 'Unknown'}<br/>
        <strong>${currentTranslations.value?.grossTonnage || 'Gross Tonnage'}:</strong> ${vessel.grossTonage || 'Unknown'}<br/>
        <strong>${currentTranslations.value?.purpose || 'Purpose'}:</strong> ${vessel.purpose || 'Unknown'}<br/>
        <strong>${currentTranslations.value?.cableCapacity || 'Cable Capacity'}:</strong> ${vessel.cableCapacity || 'Unknown'}<br/>
        <strong>${currentTranslations.value?.notes || 'Notes'}:</strong> ${vessel.notes || 'Unknown'}<br/>
        <strong>${currentTranslations.value?.baseport || 'Base Port'}:</strong> ${vessel.baseport || 'Unknown'}<br/>
        <strong>${currentTranslations.value?.owner || 'Owner'}:</strong> ${vessel.owner || 'Unknown'}<br/>
        <a href="#" id="getVesselTrack">${currentTranslations.value?.viewVesselTrack || 'View Vessel Track'}</a><br/>
        <a href="https://www.vesselfinder.com/vessels/details/${vessel.ssvid}" target="_blank">
          ${currentTranslations.value?.viewOn || 'View on VesselFinder'}
        </a>
      </div>
    `;

    // Create and configure the popup
    const popup = L.popup({
      maxWidth: 300,
      className: 'vessel-popup'
    });

    // Set popup location and content
    popup.setLatLng([
      e.latlng.lat || vessel.position.lat,
      e.latlng.lng || vessel.position.lon
    ]);
    popup.setContent(popupContent);

    // Remove existing popup if any
    if (vessel.popup && mapInstance.hasLayer(vessel.popup)) {
      mapInstance.removeLayer(vessel.popup);
    }

    // Add the new popup to the map
    popup.addTo(mapInstance);

    // Store the popup reference
    vessel.popup = popup;

    // Add event listener after popup is added to DOM
    nextTick(() => {
      const trackLink = document.getElementById("getVesselTrack");
      if (trackLink) {
        trackLink.addEventListener("click", function(event) {
          event.preventDefault();
          viewVesselTrack(vessel);
        });
      }
    });

  } catch (error) {
    console.error('Error in onShipClick:', error);
    // Try to provide more specific error information
    if (error instanceof Error) {
      console.error('Error details:', error.message);
    }
  }
};

// Add resize observer to update timeline width when map size changes
onMounted(() => {
  if (props.mapInstance) {
    const resizeObserver = new ResizeObserver(() => {
      // Force reactive update of computed style
      nextTick(() => {
        const mapBounds = props.mapInstance.getContainer().getBoundingClientRect();
        if (vesselTrackingSelected.value) {
          const container = document.querySelector('.date-popup-vesselTrack');
          if (container) {
            container.style.width = `${mapBounds.width}px`;
            if (isSearchSelected.value) {
              container.style.left = `${mapBounds.left}px`;
            }
          }
        }
      });
    });
    
    resizeObserver.observe(props.mapInstance.getContainer());
    
    // Cleanup observer on component unmount
    onUnmounted(() => {
      resizeObserver.disconnect();
      exitVesselTrackingMode();
    });
  }
});

