            </svg>
          </button>
        </div>
        
        <div class="sources-list">
          <div v-for="(source, index) in sources" 
               :key="index" 
               class="source-item">
            <h4 class="source-title">{{ source.title }}</h4>
            <div class="source-details">
              <p v-if="source.sourceName" class="source-name">
                Source: {{ source.sourceName }}
