<script setup>
import { ref, onMounted, onUnmounted } from 'vue';

const showSources = ref(false);

const sources = [
  {
    title: 'Cable Source "Telegeography"',
    url: 'https://www2.telegeography.com/',
    description: 'Mixed with own data'
  },
  {
    title: 'Cable Source "Mixed Sources"',
    url: 'https://kis-orca.org/map/',
    description: 'Mixed with telegeography and own data'
  },
  {
    title: 'Seismic Activity',
    url: 'https://earthquake.usgs.gov/earthquakes/map/',
    sourceName: 'U.S. Geological Survey (USGS)'
  },
  {
    title: 'Fishing Activity',
    url: 'https://globalfishingwatch.org/our-apis/documentation#4wings-introduction',
    sourceName: 'Global Fishing Watch'
  },
  {
    title: 'Ocean Depth',
    url: 'https://www.gebco.net/data_and_products/gridded_bathymetry_data/',
    sourceName: 'General Bathymetric Chart of the Oceans (GEBCO)'
  },
  {
    title: 'Anchorage',
    url: 'https://globalfishingwatch.org/datasets-and-code-anchorages/',
    sourceName: 'Global Fishing Watch'
  },
  {
    title: 'Fixed Infrastructure',
    url: 'https://globalfishingwatch.org/our-apis/documentation#get-fixed-infrastructure-in-mvt-http-request',
    sourceName: 'Global Fishing Watch'
  },
  {
    title: 'Seamarks',
    url: 'https://wiki.openstreetmap.org/wiki/Seamarks/Seamark_Objects',
    sourceName: 'OpenStreetMap'
  },
  {
    title: 'EEZ, Territorial Waters',
    url: 'https://marineregions.org/downloads.php',
    sourceName: 'Marine Regions'
  },
  {
    title: 'Gas Pipelines',
    url: 'https://globalenergymonitor.org/projects/global-gas-infrastructure-tracker/',
    sourceName: 'Global Energy Monitor'
  },
  {
    title: 'Cable Ships',
    url: 'https://globalfishingwatch.org/our-apis/documentation#vessels-api',
    sourceName: 'Global Fishing Watch'
  },
  {
    title: 'Map',
    url: 'https://www.maptiler.com/',
    sourceName: 'MapTiler'
  }
];

// Close sources on escape key
const handleKeydown = (event) => {
  if (event.key === 'Escape' && showSources.value) {
    showSources.value = false;
  }
};

// Add and remove event listener
onMounted(() => {
  document.addEventListener('keydown', handleKeydown);
});

onUnmounted(() => {
  document.removeEventListener('keydown', handleKeydown);
});
</script>

