<script setup lang="ts">
import { ref, watch, onMounted } from 'vue';
import axios from 'axios';
import L from 'leaflet';

const props = defineProps({
  mapInstance: { type: Object, required: true },
  drawnGeometry: { type: Object, default: null },
  samplePointCount: { type: Number, default: 200 },
  showPopup: { type: Boolean, default: false }
});

const emit = defineEmits(['depth-analysis-complete']);

const depthAnalysis = ref<{
  averageDepth: number;
  deepestPoint: number;
  shallowestPoint: number;
  samplePoints: L.LatLng[];
} | null>(null);

const isAnalyzing = ref(false);

const analysisStore = ref(new Map<string, any>());
let geometryIdCounter = 0;

interface PopupData {
  popup: L.Popup;
  analysis: {
    averageDepth: number;
    deepestPoint: number;
    shallowestPoint: number;
    samplePoints: L.LatLng[];
  };
}
const geometryPopups = new Map<L.Layer, PopupData>();
const geometryAnalyses = new Map();
const analysisData = new WeakMap();

// Calculate distance between two points in meters
function calculateDistance(point1: L.LatLng, point2: L.LatLng): number {
  const R = 6371000; // Earth's radius in meters
  const lat1 = point1.lat * Math.PI / 180;
  const lat2 = point2.lat * Math.PI / 180;
  const deltaLat = (point2.lat - point1.lat) * Math.PI / 180;
  const deltaLng = (point2.lng - point1.lng) * Math.PI / 180;

  const a = Math.sin(deltaLat/2) * Math.sin(deltaLat/2) +
           Math.cos(lat1) * Math.cos(lat2) *
           Math.sin(deltaLng/2) * Math.sin(deltaLng/2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
  return R * c;
}

// Interpolate points along a line with proportional distribution
function interpolateLinePoints(segments: L.LatLng[], totalPoints: number): L.LatLng[] {
  // Calculate total length and individual segment lengths
  let totalLength = 0;
  const segmentLengths: number[] = [];
  
  for (let i = 0; i < segments.length - 1; i++) {
    const length = calculateDistance(segments[i], segments[i + 1]);
    segmentLengths.push(length);
    totalLength += length;
  }

  // Calculate points per segment based on segment length ratio
  const pointsPerSegment = segmentLengths.map(length => 
    Math.max(2, Math.round((length / totalLength) * totalPoints))
  );

  // Generate points for each segment
  const interpolatedPoints: L.LatLng[] = [];
  for (let i = 0; i < segments.length - 1; i++) {
    const start = segments[i];
    const end = segments[i + 1];
    const numPoints = pointsPerSegment[i];

    for (let j = 0; j < numPoints; j++) {
      const fraction = j / (numPoints - 1);
      const interpolatedLat = start.lat + fraction * (end.lat - start.lat);
      const interpolatedLng = start.lng + fraction * (end.lng - start.lng);
      interpolatedPoints.push(L.latLng(interpolatedLat, interpolatedLng));
    }
  }

  // Add the last point of the last segment
  interpolatedPoints.push(segments[segments.length - 1]);
  
  return interpolatedPoints;
}

function isPointInPolygon(point: L.LatLng, polygon: L.LatLng[]): boolean {
  let inside = false;
  const x = point.lng, y = point.lat;
  for (let i = 0, j = polygon.length - 1; i < polygon.length; j = i++) {
    const xi = polygon[i].lng, yi = polygon[i].lat;
    const xj = polygon[j].lng, yj = polygon[j].lat;
    const intersect = ((yi > y) !== (yj > y)) && (x < (xj - xi) * (y - yi) / (yj - yi) + xi);
    if (intersect) inside = !inside;
  }
  return inside;
}

// sampling for polygons
function samplePolygonPoints(polygon: L.Polygon, numPoints = 500): L.LatLng[] {
  const bounds = polygon.getBounds();
  const polygonLatLngs = polygon.getLatLngs()[0] as L.LatLng[];
  const points: L.LatLng[] = [];
  
  // Adjust numPoints based on polygon area
  const area = L.GeometryUtil.geodesicArea(polygonLatLngs);
  const adjustedPoints = Math.min(Math.max(numPoints, 100), props.samplePointCount);
  
  const maxAttempts = adjustedPoints * 10;
  let attempts = 0;
  
  while (points.length < adjustedPoints && attempts < maxAttempts) {
    const lat = bounds.getSouth() + Math.random() * (bounds.getNorth() - bounds.getSouth());
    const lng = bounds.getWest() + Math.random() * (bounds.getEast() - bounds.getWest());
    const point = L.latLng(lat, lng);
    
    if (bounds.contains(point) && isPointInPolygon(point, polygonLatLngs)) {
      points.push(point);
    }
    attempts++;
  }
  
  return points;
}

// sampling for circles
function sampleCirclePoints(circle: L.Circle, numPoints = 250): L.LatLng[] {
  const center = circle.getLatLng();
  const radius = circle.getRadius();
  const adjustedPoints = Math.min(Math.max(numPoints, 50), props.samplePointCount);
  const points: L.LatLng[] = [center];
  
  for (let i = 0; i < adjustedPoints; i++) {
    const angle = Math.random() * 2 * Math.PI;
    const r = Math.sqrt(Math.random()) * radius;
    const lat = center.lat + (r / 111000) * Math.sin(angle);
    const lng = center.lng + (r / (111000 * Math.cos(center.lat * Math.PI / 180))) * Math.cos(angle);
    points.push(L.latLng(lat, lng));
  }
  
  return points;
}

// Extract coordinates based on geometry type
function extractSamplePoints(geometry: any): L.LatLng[] {
  if (geometry instanceof L.Marker) {
    return [geometry.getLatLng()];
  } else if (geometry instanceof L.Polygon || geometry instanceof L.Rectangle) {
    return samplePolygonPoints(geometry as L.Polygon, props.samplePointCount);
  } else if (geometry instanceof L.Polyline) {
    const latlngs = geometry.getLatLngs() as L.LatLng[];
    return interpolateLinePoints(latlngs, props.samplePointCount);
  } else if (geometry instanceof L.Circle) {
    return sampleCirclePoints(geometry, props.samplePointCount);
  }
  return [];
}

// Fetch ocean depth for a single coordinate
async function fetchOceanDepth(lat: number, lng: number): Promise<number> {
  try {
    const minx = lat - 0.0001;
    const miny = lng - 0.0001;
    const maxx = lat + 0.0001;
    const maxy = lng + 0.0001;
    const url = `https://wms.gebco.net/mapserv?service=WMS&version=1.3.0&request=GetFeatureInfo&layers=GEBCO_LATEST_2&styles=&bbox=${minx},${miny},${maxx},${maxy}&width=101&height=101&crs=EPSG:4326&query_layers=GEBCO_LATEST_2&info_format=text/plain&i=50&j=50`;
    
    const response = await axios.get(url);
    const searchString = "value_list";
    const searchIndex = response.data.indexOf(searchString);
    const extractedText = response.data.substring(searchIndex + searchString.length + 4, response.data.length - 2);
    
    return parseFloat(extractedText);
  } catch (error) {
    console.error('Error fetching ocean depth:', error);
    return NaN;
  }
}

// Analyze depths for entire geometry
async function analyzeGeometryDepths() {
  if (!props.drawnGeometry || !(props.drawnGeometry instanceof L.Layer)) return;
  
  isAnalyzing.value = true;
  const geometry = props.drawnGeometry;

  try {
    const samplePoints = extractSamplePoints(geometry);
    if (samplePoints.length === 0) {
      isAnalyzing.value = false;
      return;
    }

    const depthPromises = samplePoints.map(coord => fetchOceanDepth(coord.lat, coord.lng));
    const depths = await Promise.all(depthPromises);
    const validDepths = depths.filter(depth => !isNaN(depth));

    if (validDepths.length === 0) {
      isAnalyzing.value = false;
      return;
    }

    const fullDepthAnalysis = {
      averageDepth: Math.round(validDepths.reduce((a, b) => a + b, 0) / validDepths.length),
      deepestPoint: Math.round(Math.min(...validDepths)),
      shallowestPoint: Math.round(Math.max(...validDepths)),
      samplePoints
    };

    depthAnalysis.value = fullDepthAnalysis;
    emit('depth-analysis-complete', fullDepthAnalysis);

    if (props.showPopup && props.mapInstance && samplePoints.length > 0) {
      // Generate a unique ID for this geometry if it doesn't have one
      const geomId = `geometry_${geometryIdCounter++}`;

      // Store the analysis data with the geometry's ID
      analysisStore.value.set(geomId, fullDepthAnalysis);

      // Also store the ID on the layer itself using Leaflet's feature data
      if (geometry instanceof L.Path) {
        geometry.feature = geometry.feature || {};
        geometry.feature.properties = geometry.feature.properties || {};
        geometry.feature.properties.analysisId = geomId;
      }

      // Create popup content function
      const createPopupContent = (analysis: typeof fullDepthAnalysis) => `
        <div style="padding: 10px;">
          <strong>Depth Analysis:</strong>
          <ul style="padding-left: 20px; margin: 10px 0;">
            <li>Average Depth: ${analysis.averageDepth} meters</li>
            <li>Deepest Point: ${analysis.deepestPoint} meters</li>
            <li>Shallowest Point: ${analysis.shallowestPoint} meters</li>
            <li>Total Sample Points: ${analysis.samplePoints.length}</li>
          </ul>
        </div>
      `;

      // Remove existing click handler
      geometry.off('click');

      // Add click handler
      geometry.on('click', function(e: L.LeafletMouseEvent) {        
        const layer = e.target;
        
        if (layer instanceof L.Path && layer.feature && layer.feature.properties) {
          const analysisId = layer.feature.properties.analysisId;
          const analysis = analysisStore.value.get(analysisId);
          
          if (analysis) {
            L.popup()
              .setLatLng(e.latlng)
              .setContent(createPopupContent(analysis))
              .openOn(props.mapInstance);
          }
        }
      });

      // Show initial popup
      const initialPopup = L.popup()
        .setLatLng(samplePoints[0])
        .setContent(createPopupContent(fullDepthAnalysis))
        .openOn(props.mapInstance);

    }
  } catch (error) {
    console.error('Error analyzing geometry depths:', error);
  } finally {
    isAnalyzing.value = false;
  }
}

watch(() => props.drawnGeometry, (newGeometry, oldGeometry) => {
  if (newGeometry) {
    analyzeGeometryDepths();
  }
});


onMounted(() => {
  if (props.drawnGeometry) {
    analyzeGeometryDepths();
  }
});

defineExpose({
  analyzeGeometryDepths,
  depthAnalysis,
});
</script>

<template>
  <div v-if="depthAnalysis && props.showPopup" class="depth-analysis-popup">
    <h3><strong> Ocean Depth Analysis</strong></h3>
    <p><strong>for <span class="highlight-green">last geometry</span> drawn</strong></p>
    <div class="analysis-content">
      <p><strong>Average Depth:</strong> {{depthAnalysis.averageDepth}}m</p>
      <p><strong>Deepest Point:</strong> {{depthAnalysis.deepestPoint}}m</p>
      <p><strong>Shallowest Point:</strong> {{depthAnalysis.shallowestPoint}}m</p>
    </div>
  </div>
  <!-- Loading overlay -->
  <div v-if="isAnalyzing" class="loading-overlay">
    <div class="spinner"></div>
    <p>Analyzing depth data...</p>
  </div>
</template>

