  border: none;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  max-width: 300px;
  width: auto;
  max-height: 200px;
  overflow-y: auto;
  z-index: 3000;
