      </l-marker>

<!--       <l-geo-json 
        :geojson="maintenanceZonesJson"
        :options-style="getMaintenanceZonesStyle(feature)"
        @click="(e) => onMaintenanceZoneClick(e)"
        @mouseover="(e) => onMaintenanceZoneHover(e)"
        @mouseout="(e) => onMaintenanceZoneDehover(e)"
      ></l-geo-json>
      <l-geo-json
        :geojson="privateMaintenanceZonesJson"
        :options-style="getMaintenanceZonesStyle(feature)"
        @click="(e) => onMaintenanceZoneClick(e)"
        @mouseover="(e) => onMaintenanceZoneHover(e)"
        @mouseout="(e) => onMaintenanceZoneDehover(e)"
      ></l-geo-json> -->

    </l-layer-group>

    <!--Combined Anchorage and Seabed Use Layer-->
    <l-layer-group 
      name="Anchorage and seabed use" 
      layer-type="overlay" 
      :visible="false"
    >
      <!-- Seamarks Layer -->
      <l-tile-layer
        name="Seamarks"
        url="https://tiles.openseamap.org/seamark/{z}/{x}/{y}.png"
