          :radius="5"
          color="#FFFFFF"
          fill-color="#1d8f82"
          :opacity="1"
          :fill-opacity="1"
          :weight="1"
        >
          <l-tooltip>
            {{ getTranslatedInfrastructureName(infrastructure) }}, 
            {{ currentTranslations.since }}: {{ infrastructure.lastSeen }}
          </l-tooltip>
        </l-circle-marker>
      </l-layer-group>
    </l-layer-group>

    <!-- sonstige Layer -->
    <l-layer-group name="Base Layer" layer-type="base" :visible="true">
    </l-layer-group>
    
    <!-- individual geoJSONs -->
    <l-layer-group
      :visible="false"
      name="Gas Pipelines"
      layer-type="overlay"
    >
      <l-geo-json
        :geojson="gasPipelinesJson"
        :options-style="getGasPipelinesOptionsStyle()[0]"
        @click="(e) => onPipelineClick(e)"
        @mouseover="(e) => onPipelineHover(e)"
        @mouseout="(e) => onPipelineDehover(e)"
        >
      ></l-geo-json>
      <l-geo-json
        :geojson="gasPipelinesJson"
        :options-style="getGasPipelinesOptionsStyle()[1]"
        @click="(e) => onPipelineClick(e)"
        @mouseover="(e) => onPipelineHover(e)"
        @mouseout="(e) => onPipelineDehover(e)"
        >
      ></l-geo-json>
    </l-layer-group>

    <l-geo-json
      v-if="oceanSelected"
      :geojson="countries"
      name="countries"
      :visible="true"
      :options-style="{ color: '#000000', weight: '0.75' }"
    ></l-geo-json>


    <div @click="questionMarkClicked" class="question-mark">
      <h2>?</h2>
    </div>

    <div @click="showAbortModal = true" class="abort-evaluation-button">
  <h2></h2>
</div>

<!-- Abort Evaluation Modal -->
<div v-if="showAbortModal" class="abort-modal-overlay" @click="showAbortModal = false">
  <div class="abort-modal" @click.stop>
    <h3>{{ currentTranslations.abortEvaluation || 'Abort Evaluation' }}</h3>
    <p>{{ currentTranslations.abortEvaluationText || 'Are you sure you want to abort the evaluation? This will remove your current session replay.' }}</p>
    <div class="modal-buttons">
      <button @click="showAbortModal = false" class="cancel-button">
        {{ currentTranslations.cancel || 'Cancel' }}
      </button>
      <button @click="abortEvaluation" class="confirm-button">
        {{ currentTranslations.confirm || 'Confirm' }}
      </button>
    </div>
  </div>
</div>

    <FunctionalityOverview ref="functionalityOverviewRef"/>

    <!-- Ocean Legende Popup -->
    <div v-if="oceanSelected" class="custom-popup">
      <h2>{{ currentTranslations.oceanMapLegend }}</h2>
      <p>
        <span style="color: #a2c3e2">&#9632;</span>
        : > -200m
      </p>
      <p>
        <span style="color: #6d9cc8">&#9632;</span>
        : > -1000m
      </p>
      <p>
        <span style="color: #426e9d">&#9632;</span>
        : > -6000m
      </p>
      <p>
        <span style="color: #214d7c">&#9632;</span>
        : > -11000m
      </p>
    </div>

    <div v-if="oceanSelected" class="custom-popup3">
      <p>{{ currentTranslations.latitude }}: {{ lattitude }}</p>
      <p>{{ currentTranslations.longitude }}: {{ longitude }}</p>
    </div>

    <div v-if="fishingSelected" class="date-popup">
      <VueDatePicker
        v-model="date"
        :model-value="date"
        @update:model-value="datePicked"
