    <div v-if="showSeismicOverlay" class="date-popup">
  <VueDatePicker
    v-model="seismicDate"
    :model-value="seismicDate"
    @update:model-value="seismicDatePicked"
    :placeholder="currentTranslations.datePlaceholderSeismic"
    :preview-format="format"
    :format="format"
    :enable-time-picker="false"
    :range="{ partialRange: false }"
    dark=true
    class="custom-datepicker"
    no-today
    :max-date="new Date()"
    />
  </div>

    <div class="language-selector">
      <div class="language-icon"></div>
      <select v-model="currentLanguage" @change="changeLanguage(currentLanguage)">
        <option value="ENGLISH">English</option>
        <option value="GERMAN">Deutsch</option>
        <option value="SPANISH">Espaol</option>
        <option value="FRENCH">Franais</option>
        <option value="ITALIAN">Italiano</option>
        <option value="CHINESE"></option>
      </select>
    </div>

    <div class="cable-source-selector">
      <CableSourceSelector />
    </div>

      <!-- Clustered Anchorage -->
    <ClusteredAnchorage 
      :mapInstance="mapInstance"
      :controlInstance="controlInstance"
      :map-ready="mapReady"
      :current-translations="currentTranslations"
    />

    <!-- Clustered Data Centers -->
    <ClusteredDataCenters
      ref="clusteredDataCentersRef"
      :mapInstance="mapInstance"
      :controlInstance="controlInstance"
      :mapReady="mapReady"
      :currentTranslations="currentTranslations"
    />

    <!-- Toggle component for Anchorage and seabed use bundled layer -->
    <AnchorageLayerToggle 
      ref="anchorageLayerToggleRef"
      :map-instance="mapInstance"
      :control-instance="controlInstance"
      :current-translations="currentTranslations"
    />

    <MaintenanceLayerToggle 
      ref="maintenanceLayerToggleRef"
      :map-instance="mapInstance"
      :control-instance="controlInstance"
      :current-translations="currentTranslations"
      :viewingVesselTrack="viewingVesselTrack"
    />

    <!-- Draw Function for Depth Measurement Tool -->
    <div v-show="oceanSelected">
      <DrawingLayer 
        :map-instance="mapInstance"
        :oceanSelected="oceanSelected"
        ref="drawingLayerRef" 
      />
    </div>

  </l-map>

  <div v-if="selectedEarthquake" class="earthquake-details">
    <h2>{{ currentTranslations.selectedEarthquakeDetails }}:</h2>
    <p><strong>{{ currentTranslations.value.magnitude }}:</strong> {{ selectedEarthquake.mag }}</p>
    <p><strong>{{ currentTranslations.value.location }}:</strong> {{ selectedEarthquake.place }}</p>
    <p><strong>{{ currentTranslations.value.time }}:</strong> {{ new Date(selectedEarthquake.time).toLocaleString() }}</p>
  </div>

  <div v-if="oceanSelected && !isDrawingModeActive" ref="depthTooltip" class="depth-tooltip">
    {{ currentOceanDepth }}
  </div>
</template>

<script setup lang="ts">
import FunctionalityOverview from './FunctionalityOverview.vue';

import { ref, onMounted, watch, toRaw, computed, onBeforeUnmount } from 'vue';
import L from 'leaflet';
import 'leaflet.wms';
//import tracker from '@/openreplay';
import { tracker } from '@openreplay/tracker';

import 'leaflet/dist/leaflet.css';
// Import the PolylineMeasure plugin
import 'leaflet.polylinemeasure/Leaflet.PolylineMeasure.css';
import 'leaflet.polylinemeasure/Leaflet.PolylineMeasure';

import axios from 'axios';
import {
  LMap,
  LTileLayer,
  LControlLayers,
  LTooltip,
  LPolyline,
  LMarker,
  LCircleMarker,
  LGeoJson,
  LLayerGroup,
} from '@vue-leaflet/vue-leaflet';
import { useDataCablesStore } from '@/store/datacables';
import { useLandingSitesStore } from '@/store/landingSites';


//Cable Selection
import CableSourceSelector from './CableSourceSelector.vue';

import DrawingLayer from './DrawingLayer.vue'
//Translation
import translations from "@/assets/translations/translations.json";
import layerTranslations from "@/assets/translations/layerTranslations.json";

//Cable Ships
import VesselTracking from './VesselTracking.vue';
import MaintenanceLayerToggle from './MaintenanceLayerToggle.vue';

//MaritimeZone Imports
import eezBoundariesJson from "@/assets/eez_boundaries_v12.json";
import eez12nmJson from "@/assets/eez_12nm_v4.json";

import countries from "@/assets/countries.json";

import VueDatePicker from "@vuepic/vue-datepicker";
import '@vuepic/vue-datepicker/dist/main.css'
//GasPipelines
import gasPipelinesJson from "@/assets/GEM-GGIT-Gas-Pipelines-2023-12.json";
//Infrastructures
import fixedInfrastructure from "@/assets/fixedInfrastructure.json";
import ClusteredDataCenters from './ClusteredDataCenters.vue';

//Anchorage
import AnchorageLayerToggle from './AnchorageLayerToggle.vue';
import ClusteredAnchorage from './ClusteredAnchorage.vue';

import debounce from "lodash.debounce";

import "@maptiler/leaflet-maptilersdk";
import 'leaflet-arrowheads';

// Add this near your other ref declarations
const showAbortModal = ref(false);

// Add this function
const abortEvaluation = () => {
  try {
    // Access the global tracker instance
    const tracker = (window as any)._trackerInstance;
    
    if (tracker) {
      // Stop the current session
      tracker.stop();
      console.log('Session replay aborted');
    }
    
    // Close the modal
    showAbortModal.value = false;
    
    // Optional: Show a confirmation message
    // You could add a toast notification here if you have one
    
  } catch (error) {
    console.error('Error aborting evaluation:', error);
    showAbortModal.value = false;
  }
};

const cableStore = useDataCablesStore();
const landingSiteStore = useLandingSitesStore();

// Define reactive data
const zoom = ref(2.5);
const center = ref([47.41322, -1.219482]);
const circle = {
  radius: 3,
  selectedRadius: 6,
  color: 'grey',
  selectedColor: 'red',
};
const modifiedPostUrl = ref('https://{s}.tile.openstreetmap.de/{z}/{x}/{y}.png');
const date =  ref();
const map = ref(null);
const mapReady = ref(false);

function getTerritorialOptionsStyle(){
  return {
    color: '#4a2727',
    dashArray: '8',
    fillColor: '#4a2727',
    fillOpacity: '0.2',
  };
}

function getGasPipelinesOptionsStyle() {
  return [
    // Border style
    (feature: any) => {
      const isPipelineHighlighted = highlightedPipelineId.value === feature.properties.PipelineID;
      return {
        color: isPipelineHighlighted ? '#FF5733' : '#333333',
        weight: isPipelineHighlighted ? 6 : 4,
        opacity: isPipelineHighlighted ? 0.9 : 0.5,
        dashArray: '5',
      };
    },
    // Fill style
    (feature: any) => {
      const isPipelineHighlighted = highlightedPipelineId.value === feature.properties.PipelineID;
      return {
        color: isPipelineHighlighted ? '#FFAA33' : '#3dff98',
        weight: isPipelineHighlighted ? 1.2 : 0.7,
        opacity: isPipelineHighlighted ? 0.8 : 0.5,
      };
    }
  ];
}

const highlightedPipelineId = ref<string | null>(null);

const clusteredDataCentersRef = ref(null);

function getEEZOptionsStyle(){
  return{
    color: '#8a3030', 
    dashArray: '8'
  }
}

// Translation
const currentLanguage = ref('ENGLISH');
const changeLanguage = (language) => {
  currentLanguage.value = language;
  if (mapInstance.value) {
    mtLayer.setLanguage(L.MaptilerLanguage[language]);
    updateLayerControlNames();
  }
};
function getLayerTranslation(layerName) {
  if (layerTranslations[currentLanguage.value] && layerTranslations[currentLanguage.value][layerName]) {
    return layerTranslations[currentLanguage.value][layerName];
  }
  return layerName;
}
const currentTranslations = computed(() => translations[currentLanguage.value]);
const getTranslatedInfrastructureName = (infrastructure) => {
  const name = currentTranslations.value[infrastructure.name] || currentTranslations.value.unknown;
  const confidence = currentTranslations.value[infrastructure.confidence];
  return `${name} (${currentTranslations.value.confidence} ${confidence})`;
};
//Reset Map Layer
const resetMapLayer = ref(null);

let resetMapCallCount = 0;

const resetMap = () => {
  if (!mapInstance.value || !controlInstance) {
    console.warn('Map instance or control instance not available');
    return;
  }

  // Remove all overlay layers from the map
  controlInstance._layers.forEach(layerObj => {
    if (layerObj.overlay && mapInstance.value.hasLayer(layerObj.layer)) {
      mapInstance.value.removeLayer(layerObj.layer);
    }
  });

  // Uncheck all overlay layers in the layer control
  if (controlInstance._layerControlInputs) {
    controlInstance._layerControlInputs.forEach(input => {
      if (input.overlay) {
        input.checked = false;
      }
    });
  }

  // Force control to update its display
  controlInstance._update();

  // Remove class modifiers from map container
  const mapContainer = mapInstance.value._container;
  mapContainer.classList.remove(
    'ocean-depth-active',
    'fishing-active',
    'seismic-active',
    'anchorage-active',
    'cable-ships-active'
  );

  // Reset state variables
  oceanSelected.value = false;
  fishingSelected.value = false;
  showSeismicOverlay.value = false;

  // Exit vessel tracking mode if active
  if (vesselTrackingRef.value?.isVesselTrackingMode.value) {
    vesselTrackingRef.value.exitVesselTrackingMode();
  }

  // Clear any selections
  cableStore.deselectCables();
  landingSiteStore.deselectLandingSite();

  // Hide any open toggles
  if (anchorageLayerToggleRef.value) {
    anchorageLayerToggleRef.value.hide();
  }
  if (maintenanceLayerToggleRef.value) {
    maintenanceLayerToggleRef.value.hide();
  }

  // Switch to the default base layer
  const baseLayer = controlInstance._layers.find(layer => 
    !layer.overlay && layer.originalName === "Base Layer"
  );
  
  if (baseLayer && baseLayer.layer) {
    // Remove current base layer if any
    controlInstance._layers
      .filter(layer => !layer.overlay && mapInstance.value.hasLayer(layer.layer))
      .forEach(layer => mapInstance.value.removeLayer(layer.layer));
    
    // Add Base Layer
    mapInstance.value.addLayer(baseLayer.layer);
    
    // Update layer control
    controlInstance._update();
  }
};
//Fishing
const format = "dd/MM/yyyy";
const fishLayerReady = ref(false);
const postResponse = ref(null);
const fishingSelected = ref(false);

//Vessel Tracking
const hoveredMarker = ref(null);
const eventMarkerLayer = ref(null);
const viewingVesselTrack = ref(false);

const vesselTrackingRef = ref<any>(null);
const vesselLayerReady = ref(false);
const vesselPositions = ref([]);
watch(() => vesselTrackingRef.value?.vesselLayerReady, (newValue) => {
  vesselLayerReady.value = newValue;  
}, { immediate: true });

 // Watch for vesselPositions from VesselTracking
watch(
  () => vesselPositions,
  (newPositions) => {
    if (newPositions && Array.isArray(newPositions) && newPositions.length > 0) {
      vesselPositions.value = newPositions;
    }
  },
  { deep: true }
);

//Watch for hoveredMarker changes and either add Marker or clear Marker
watch(
  () => hoveredMarker.value,
  (newMarker) => {
    if (newMarker && eventMarkerLayer.value) {
      eventMarkerLayer.value.addLayer(newMarker);
    } else if (eventMarkerLayer.value && newMarker === null) {
      eventMarkerLayer.value.clearLayers();
    }
  },
  { deep: true }
);
interface Vessel {
  id: string;
  position: [number, number];
  flag: string;
}

const exitVesselTrackingMode = () => {
  vesselTrackingRef.value.exitVesselTrackingMode();
}

const createShipIcon = (vessel: Vessel, caller : string) => {
  if (!vesselTrackingRef.value) {
    console.warn('VesselTracking component reference not available');
    return null;
  }
  return vesselTrackingRef.value.createShipIcon('#ff7800', vessel.flag, vessel);
};

// Wrapper function to call onShipClick from VesselTracking
const onShipClick = (e: Event, vessel: Vessel) => {
  if (!vesselTrackingRef.value) {
    console.warn('VesselTracking component reference not available');
    return;
  }
  vesselTrackingRef.value.onShipClick(e, vessel);
};

const maintenanceLayerToggleRef = ref(null);
//Fixed Infrastructure
const anchorageLayerToggleRef = ref(null);
const clusteredAnchorageRef = ref(null);

const infrastructurePositions = ref([]);
const processInfrastructureData = () => {
  infrastructurePositions.value = fixedInfrastructure.results
    .filter(item => !(item.label === "noise") && item.structure_end_date === "")
    .map(item => ({
      id: item.detection_id,
      position: [item.lat, item.lon],
      name: item.label,
      confidence: item.label_confidence,
      lastSeen: item.detection_date
    }));
};

const onPipelineClick = (e) => {
  L.DomEvent.stopPropagation(e);
  const feature = e.layer.toGeoJSON();
  const properties = feature.properties;
  const pipelineId = properties.PipelineID;

  // Store the previously highlighted pipeline ID before updating
  const previouslyHighlighted = highlightedPipelineId.value;
  
  // Toggle highlighting - if clicking same pipeline, deselect it
  if (highlightedPipelineId.value === pipelineId) {
    highlightedPipelineId.value = null;
  } else {
    highlightedPipelineId.value = pipelineId;
  }

  // Force redraw of all pipeline segments
  if (mapInstance.value) {
    mapInstance.value.eachLayer((layer) => {
      if (layer.feature && layer.feature.properties) {
        const isSamePipeline = layer.feature.properties.PipelineID === pipelineId;
        const wasPreviouslyHighlighted = layer.feature.properties.PipelineID === previouslyHighlighted;
        
        // Only update layers that are either newly selected or previously selected
        if (isSamePipeline || wasPreviouslyHighlighted) {
          const [borderStyle, fillStyle] = getGasPipelinesOptionsStyle();
          
          // Check if this is a border layer or a fill layer
          const currentStyle = layer.options;
          
          if (currentStyle.dashArray) {
            layer.setStyle(borderStyle(layer.feature));
          } else {
            layer.setStyle(fillStyle(layer.feature));
          }
        }
      }
    });
  }

  // Show popup with pipeline info if a new pipeline was selected
  if (highlightedPipelineId.value === pipelineId) {
    const cost = properties.CostUSD ?? properties.Cost;
    const formattedCost = cost ? formatCost(cost) : 'N/A';
    const popupContent = `<strong>${properties.PipelineName}</strong><br/>
      <strong>${currentTranslations.value.startCountry}:</strong> ${properties.StartCountry}<br/>
      <strong>${currentTranslations.value.endCountry}:</strong> ${properties.EndCountry}<br/>
      <strong>${currentTranslations.value.countries}:</strong> ${properties.Countries}<br/>
      <strong>${currentTranslations.value.owner}:</strong> ${properties.Owner}<br/>
      <strong>${currentTranslations.value.startYear}:</strong> ${properties.StartYear1}<br/>
      <strong>${currentTranslations.value.capacity}:</strong> <span title="${currentTranslations.value.capacityTooltip}">${properties["CapacityBcm/y"]} Bcm/y</span><br/>
      <strong>${currentTranslations.value.cost}:</strong> ${formattedCost}$<br/>
      <strong>${currentTranslations.value.length}:</strong> ${properties.LengthKnownKm} km<br/>`;
    
    const popup = L.popup()
      .setLatLng(e.latlng)
      .setContent(popupContent)
      .openOn(mapInstance.value);
  }
};

// Utility function to format large numbers into billions or millions
const formatCost = (cost) => {
  if (cost >= 1e9) {
    return `${(cost / 1e9).toFixed(2)} B`;
  } else if (cost >= 1e6) {
    return `${(cost / 1e6).toFixed(2)} M`;
  } else {
    return cost.toString();
  }
};

