const onPipelineClick = (e) => {
  L.DomEvent.stopPropagation(e);
  const feature = e.layer.toGeoJSON();
  const properties = feature.properties;
  const pipelineId = properties.PipelineID;

  // Store the previously highlighted pipeline ID before updating
  const previouslyHighlighted = highlightedPipelineId.value;
  
  // Toggle highlighting - if clicking same pipeline, deselect it
  if (highlightedPipelineId.value === pipelineId) {
    highlightedPipelineId.value = null;
  } else {
    highlightedPipelineId.value = pipelineId;
  }

  // Force redraw of all pipeline segments
  if (mapInstance.value) {
    mapInstance.value.eachLayer((layer) => {
      if (layer.feature && layer.feature.properties) {
        const isSamePipeline = layer.feature.properties.PipelineID === pipelineId;
        const wasPreviouslyHighlighted = layer.feature.properties.PipelineID === previouslyHighlighted;
        
        // Only update layers that are either newly selected or previously selected
        if (isSamePipeline || wasPreviouslyHighlighted) {
          const [borderStyle, fillStyle] = getGasPipelinesOptionsStyle();
          
          // Check if this is a border layer or a fill layer
          const currentStyle = layer.options;
          
          if (currentStyle.dashArray) {
            layer.setStyle(borderStyle(layer.feature));
          } else {
            layer.setStyle(fillStyle(layer.feature));
          }
        }
      }
    });
  }

  // Show popup with pipeline info if a new pipeline was selected
  if (highlightedPipelineId.value === pipelineId) {
    const cost = properties.CostUSD ?? properties.Cost;
    const formattedCost = cost ? formatCost(cost) : 'N/A';
    const popupContent = `<strong>${properties.PipelineName}</strong><br/>
      <strong>${currentTranslations.value.startCountry}:</strong> ${properties.StartCountry}<br/>
      <strong>${currentTranslations.value.endCountry}:</strong> ${properties.EndCountry}<br/>
      <strong>${currentTranslations.value.countries}:</strong> ${properties.Countries}<br/>
      <strong>${currentTranslations.value.owner}:</strong> ${properties.Owner}<br/>
      <strong>${currentTranslations.value.startYear}:</strong> ${properties.StartYear1}<br/>
      <strong>${currentTranslations.value.capacity}:</strong> <span title="${currentTranslations.value.capacityTooltip}">${properties["CapacityBcm/y"]} Bcm/y</span><br/>
      <strong>${currentTranslations.value.cost}:</strong> ${formattedCost}$<br/>
      <strong>${currentTranslations.value.length}:</strong> ${properties.LengthKnownKm} km<br/>`;
    
    const popup = L.popup()
      .setLatLng(e.latlng)
      .setContent(popupContent)
      .openOn(mapInstance.value);
  }
};

// Utility function to format large numbers into billions or millions
const formatCost = (cost) => {
  if (cost >= 1e9) {
    return `${(cost / 1e9).toFixed(2)} B`;
  } else if (cost >= 1e6) {
    return `${(cost / 1e6).toFixed(2)} M`;
  } else {
    return cost.toString();
  }
};

