    const tempLines = document.querySelectorAll('.leaflet-polylinemeasure-temporary-layer');
    const measureLines = document.querySelectorAll('.leaflet-polylinemeasure-permanent-layer');
    
    // Temporarily hide measurement lines during zoom
    tempLines.forEach(line => { line.style.display = 'none'; });
    measureLines.forEach(line => { line.style.display = 'none'; });
  });

  mapInstance.value.on('zoomend', () => {
    const tempLines = document.querySelectorAll('.leaflet-polylinemeasure-temporary-layer');
    const measureLines = document.querySelectorAll('.leaflet-polylinemeasure-permanent-layer');
    
