//Function to update the Layer Names on the Layer Control which is called after the language has been changed
function updateLayerControlNames() {
  if (controlInstance) {
    controlInstance._layers.forEach(layerObj => {
      if(layerObj.originalName == null) layerObj.originalName = layerObj.name;
      const translatedName = getLayerTranslation(layerObj.originalName);
      layerObj.name = translatedName;
    });
    controlInstance._update();
  }
}

