async function fishingWatch(){
  try {
    const response = await axios.post(apiUrl, null, {params: params})
      postResponse.value = response; //TODO: evtl. ColorRamp rausschmeien und nur URL speichern
      modifiedPostUrl.value = response.data.url.replace("gateway.api.globalfishingwatch.org", "sdcs-api.dev.peasec.de/fishing");
      fishLayerReady.value = true;
  } catch(error) {
    console.error(error);
  }
}

