//Extracts Ocean Depth for current mouse position from GEBCO Web Map Service
let lastDepthQuery = '';

async function getOceanDepth(lat, lng) {
  const queryString = `${lat.toFixed(4)},${lng.toFixed(4)}`;
  if (queryString === lastDepthQuery) return;
  lastDepthQuery = queryString;
  try{
    const minx = lat - 0.0001;
    const miny = lng - 0.0001;
    const maxx = lat + 0.0001;
    const maxy = lng + 0.0001;

    const url = `https://wms.gebco.net/mapserv?service=WMS&version=1.3.0&request=GetFeatureInfo&layers=GEBCO_LATEST_2&styles=&bbox=${minx},${miny},${maxx},${maxy}&width=101&height=101&crs=EPSG:4326&query_layers=GEBCO_LATEST_2&info_format=text/plain&i=50&j=50`;
    const response = await axios.get(url);

    let searchString = "value_list";
    let searchIndex = response.data.indexOf(searchString);
    let extractedText = response.data.substring(searchIndex + searchString.length + 4, response.data.length - 2);
    currentOceanDepth.value = `${currentTranslations.value.oceanDepth}: ${extractedText}m`;
  } catch(error) {
    console.error(error);
    currentOceanDepth.value = "Ocean Depth: Unknown";
  }
}

//Translates picked Date from Datepicker to API Call to GlobalFishingWatch
function datePicked(e){
  if(e != null) {
    let firstDay = formatDatePart(e[0].getDate());
    let firstMonth = formatDatePart(e[0].getMonth() + 1);
    let secondDay = formatDatePart(e[1].getDate());
    let secondMonth = formatDatePart(e[1].getMonth() + 1);
    params['date-range'] = e[0].getFullYear() + '-' + firstMonth + '-' +  firstDay + ',' + e[1].getFullYear() + '-' + secondMonth + '-' + secondDay;
    fishingWatch();
  }
}

//Helper Function to formate Dates
function formatDatePart(part){
  return part < 10 ? '0' + part : part;
}

// Funktion fr Layerwechsel (@baselayerchanged Listener), um Popup bei Aktivierung/Deaktivierung des Ocean Layers zu ffnen/schlieen
function layerChanged(e) {
  if (!mapInstance.value || !mapInstance.value._container) {
    console.warn('Map instance not ready during layer change');
    return;
  }
  const mapContainer = mapInstance.value._container;
  mapContainer.classList.remove(
    'ocean-depth-active', 
    'fishing-active', 
    'seismic-active'
  );

  /* if (vesselTrackingRef.value?.isVesselTrackingMode.value) {
    vesselTrackingRef.value.exitVesselTrackingMode();
  } */

  if (e.originalName === "Reset Map") {
    resetMap();
    
    // Find and switch to Base Layer
    const baseLayer = controlInstance._layers.find(layer => 
      !layer.overlay && layer.originalName === "Base Layer"
    );
    
    if (baseLayer && baseLayer.layer) {
      // Remove current base layer if any
      controlInstance._layers
        .filter(layer => !layer.overlay && mapInstance.value.hasLayer(layer.layer))
        .forEach(layer => mapInstance.value.removeLayer(layer.layer));
      
      // Add Base Layer
      mapInstance.value.addLayer(baseLayer.layer);
      
      // Update layer control
      controlInstance._update();
    }
    return;
  }
  if(e.originalName === "Base Layer"){
    oceanSelected.value = false;
    fishingSelected.value = false;
    showSeismicOverlay.value = false;
  }
  else if(e.originalName === "Seismic Activity"){
    mapContainer.classList.add('seismic-active');
    fetchSeismicData();
    oceanSelected.value = false;
    fishingSelected.value = false;
    showSeismicOverlay.value = true;
  }
  else if (e.originalName === "Fishing Activity"){
    mapContainer.classList.add('fishing-active');
    fishingSelected.value = true;
    oceanSelected.value = false;
    showSeismicOverlay.value = false;
  }
  else if (e.originalName === "Ocean Depth") {
    mapContainer.classList.add('ocean-depth-active');
    oceanSelected.value = true;
    fishingSelected.value = false;
    showSeismicOverlay.value = false;
  } else {
    oceanSelected.value = false;
    fishingSelected.value = false;
    showSeismicOverlay.value = false;
  }
}

function overlayAdded(e) {
  const mapContainer = mapInstance.value._container;
  if (e.originalName === "Anchorage and seabed use") {
    mapContainer.classList.add('anchorage-active');
    if (anchorageLayerToggleRef.value) {
      anchorageLayerToggleRef.value.show();
    }
  } else if (e.originalName === "Cable Ships") {
    mapContainer.classList.add('cable-ships-active');
    if (maintenanceLayerToggleRef.value) {
      maintenanceLayerToggleRef.value.show();
    }
  }
}

function overlayRemoved(e) {
  const mapContainer = mapInstance.value._container;

  if (e.originalName === "Anchorage and seabed use") {
    mapContainer.classList.remove('anchorage-active');
    if (anchorageLayerToggleRef.value) {
      anchorageLayerToggleRef.value.hide();
    }
  } else if (e.originalName === "Cable Ships") {
    mapContainer.classList.remove('cable-ships-active');
    if (maintenanceLayerToggleRef.value) {
      maintenanceLayerToggleRef.value.hide();
    }
  }
}

const questionMarkClicked = () => {
  if(functionalityOverviewRef.value){
    functionalityOverviewRef.value.openModal();
  }
}

let set = false;
const onMapClick = ( event ) => { // onMapClick = (e: event) => { ... }
  // TODO This is a workaround because the event is propagation to alls listeners
  // Tested: mapInstance._container.stopPropgating(e)
  // Tested: e.selected = true -> and then read in next handler
  // Tested: @click.stop didn't work(see issue https://github.com/vue-leaflet/vue-leaflet/issues/362)
  const previouslyHighlighted = highlightedPipelineId.value;
  highlightedPipelineId.value = null;
  
  // Force redraw of previously highlighted pipeline if there was one
  if (previouslyHighlighted && mapInstance.value) {
    mapInstance.value.eachLayer((layer) => {
      if (layer.feature && 
          layer.feature.properties && 
          layer.feature.properties.PipelineID === previouslyHighlighted) {
        
        const [borderStyle, fillStyle] = getGasPipelinesOptionsStyle();
        
        // Check if this is a border layer or a fill layer
        const currentStyle = layer.options;
        
        // Apply appropriate style based on layer type
        if (currentStyle.dashArray) {
          layer.setStyle(borderStyle(layer.feature));
        } else {
          layer.setStyle(fillStyle(layer.feature));
        }
      }
    });
  }
  if (maintenanceLayerToggleRef.value) {
    const targetElement = event.originalEvent.target;
    if (targetElement && !targetElement.classList.contains('leaflet-interactive')) {
      maintenanceLayerToggleRef.value.clearHighlightedZones();
    }
  }  

  if(!set) {
    cableStore.deselectCables();
    landingSiteStore.deselectLandingSite();
  }
  set = false
}

const onCableClick = (e, cable) => {
  set = true
  if(!landingSiteStore.isOneSelected() && cableStore.isCableSelected(cable.id)) {
    cableStore.deselectCables();
  } else {
    landingSiteStore.deselectLandingSite();
    cableStore.setSelectCable(cable.id, false) // Set fromSidebar to false
  }
  
  // Display cable name at click position
  L.popup()
    .setLatLng(e.latlng)
    .setContent(cable.name)
    .openOn(mapInstance.value);
}
const onLandingSiteClick = (e, landingSite) => {
  set = true
  cableStore.deselectCables();
  if(landingSiteStore.isLandingSiteSelected(toRaw(landingSite))) {
    landingSiteStore.deselectLandingSite();
  } else {
    landingSiteStore.setSelectLandingSite(toRaw(landingSite))
  }
}

const updateMousePosition = debounce((e) => {
  if(oceanSelected.value){
    const latlng = e.latlng;
    lattitude.value = latlng.lat;
    longitude.value = latlng.lng;
    if(!isDrawingModeActive.value){
      getOceanDepth(latlng.lat, latlng.lng);
      updateDepthTooltip(e);
    }
  }
}, 100);

//SEISMIC LAYER
const seismicDatePicked = (e) => {
  if (e != null) {
    let startDate = formatDatePart(e[0].getDate());
    let startMonth = formatDatePart(e[0].getMonth() + 1);
    let endDate = formatDatePart(e[1].getDate());
    let endMonth = formatDatePart(e[1].getMonth() + 1);
    const startTime = `${e[0].getFullYear()}-${startMonth}-${startDate}`;
    const endTime = `${e[1].getFullYear()}-${endMonth}-${endDate}`;
    // Clear existing seismic layer
    if (seismicLayer.value) {
      mapInstance.value.removeLayer(seismicLayer.value);
      seismicLayer.value = null;
    }
    
    fetchSeismicData(startTime, endTime);
  } else {
    // Clear existing seismic layer
    if (seismicLayer.value) {
      mapInstance.value.removeLayer(seismicLayer.value);
      seismicLayer.value = null;
    }
    
    fetchSeismicData();
  }
};

const fetchSeismicData = async (startTime = null, endTime = null) => {
  try {
    let url;
    if (startTime && endTime) {
      // Fetch data for the specified date range
      url = `https://earthquake.usgs.gov/fdsnws/event/1/query?format=geojson&starttime=${startTime}&endtime=${endTime}`;
    } else {
      // Default to the last week's data
      url = 'https://earthquake.usgs.gov/earthquakes/feed/v1.0/summary/2.5_week.geojson';
    }
    const response = await axios.get(url);
    seismicData.value = response.data;

        // Add new seismic layer to the map
        if (mapInstance.value && seismicData.value) {
      seismicLayer.value = L.geoJSON(seismicData.value, {
        pointToLayer: reactivePointToLayer.value
      }).addTo(mapInstance.value);
    }
  } catch (error) {
    console.error('Error fetching seismic data:', error);
  }
};

