const reactivePointToLayer = computed(() => (feature, latlng) => {
  const magnitude = feature.properties.mag;
  const radius = magnitude * 3;
  const fillColor = magnitude > 5 ? 'red' : magnitude > 3 ? 'orange' : 'yellow';
  return L.circleMarker(latlng, {
    radius: radius,
    fillColor: fillColor,
    color: 'black',
    weight: 1,
    opacity: 1,
    fillOpacity: 0.8
  }).bindTooltip(`
    <strong>${currentTranslations.value.magnitude}:</strong> ${magnitude}<br>
    <strong>${currentTranslations.value.location}:</strong> ${feature.properties.place}<br>
    <strong>${currentTranslations.value.time}:</strong> ${new Date(feature.properties.time).toLocaleString()}
  `);
});

