<script setup>
import { ref, onUnmounted, watch } from 'vue';
import L from 'leaflet';
import Supercluster from 'supercluster';
import anchorageData from '@/assets/anchorage.json';
import anchorSVG from '@/assets/graphics/anchor.svg';


const props = defineProps({
  mapInstance: {
    type: Object,
    required: true
  },
  controlInstance: {
    type: Object,
    required: true
  },
  mapReady: {
    type: Boolean,
    required: true
  },
  currentTranslations: {
    type: Object,
    required: true
  }
});

const supercluster = ref(new Supercluster({
  radius: 40,
  maxZoom: 16,
  map: props => ({
    label: props.label,
    sublabel: props.sublabel,
    distance: props.distance_from_shore_m,
    isDock: props.dock === 'true',
    count: 1
  }),
  reduce: (accumulated, props) => {
    accumulated.count += props.count;
    accumulated.isDock = accumulated.isDock || props.isDock;
    return accumulated;
  }
}));

const anchorageLayer = ref(null);
const isLayerVisible = ref(false);

const updateClusters = () => {
  if (!props.mapInstance || !anchorageLayer.value || !isLayerVisible.value) return;
  
  const bounds = props.mapInstance.getBounds();
  const bbox = [
    bounds.getWest(),
    bounds.getSouth(),
    bounds.getEast(),
    bounds.getNorth()
  ];
  
  const zoom = props.mapInstance.getZoom();
  const clusters = supercluster.value.getClusters(bbox, Math.floor(zoom));
  
  // Clear existing layers
  anchorageLayer.value.clearLayers();
  
  clusters.forEach(cluster => {
    const coordinates = cluster.geometry.coordinates;
    const latlng = L.latLng(coordinates[1], coordinates[0]);
    
    if (cluster.properties.cluster) {
      // Create cluster marker
      const count = cluster.properties.point_count;
      const size = count < 100 ? 'small' : count < 1000 ? 'medium' : 'large';
      
      const clusterMarker = L.divIcon({
        html: `
          <div class="cluster-marker ${size}">
            ${count}
          </div>
        `,
        className: 'custom-cluster-icon',
        iconSize: L.point(40, 40)
      });
      
      const marker = L.marker(latlng, {
        icon: clusterMarker
      });
      
      // Add click handler to zoom into cluster
      marker.on('click', () => {
        const expansionZoom = supercluster.value.getClusterExpansionZoom(cluster.properties.cluster_id);
        props.mapInstance.setView(latlng, expansionZoom);
      });
      
      anchorageLayer.value.addLayer(marker);
    } else {
      // Create individual point marker
      const anchorIcon = L.divIcon({
        html: `
          <div class="anchor-icon">
            <img src="${anchorSVG}" alt="Anchor" width="24" height="24" />
          </div>
        `,
        className: 'anchor-icon',
        iconSize: L.point(24, 24)
      });
      const marker = L.marker(latlng, { icon: anchorIcon });
      // Add tooltip
      marker.bindTooltip(`
        <strong>${cluster.properties.label}</strong><br>
        ${cluster.properties.sublabel}<br>
        ${props.currentTranslations.distanceFromShore}: ${cluster.properties.distance_from_shore_m} m<br>
        ${props.currentTranslations.isDock}: <strong>${cluster.properties.dock === 'true' ? '<span class="text-green-500"></span>' : '<span class="text-red-500"></span>'}</strong>
        <br>
      `);
      
      anchorageLayer.value.addLayer(marker);
    }
  });
};

const initializeLayer = () => {
  if (!props.mapInstance || !props.mapReady) return;
  
  // Load GeoJSON features directly into supercluster
  supercluster.value.load(anchorageData.features);
  
  // Initialize layer group if not exists
  if (!anchorageLayer.value) {
    anchorageLayer.value = L.layerGroup();
    
    // Add to map control layers
    const anchorageOverlayLayer = props.controlInstance._layers.find(
      layerObj => layerObj.originalName === "Anchorage and seabed use"
    );
    if (anchorageOverlayLayer) {
      anchorageOverlayLayer.layer.addLayer(anchorageLayer.value);
    } else {
      console.warn("Could not find Anchorage and seabed use layer group");
      // Fallback: add directly to map if layer group not found
      props.mapInstance.addLayer(anchorageLayer.value);
    }
    
    // Add event listeners for map movements
    props.mapInstance.on('moveend', updateClusters);
    props.mapInstance.on('zoomend', updateClusters);
    
    // Listen for layer add/remove events
    props.mapInstance.on('overlayadd', (e) => {
      if (e.name === 'Anchorage and seabed use') {
        isLayerVisible.value = true;
        updateClusters();
      }
    });
    
    props.mapInstance.on('overlayremove', (e) => {
      if (e.name === 'Anchorage and seabed use') {
        isLayerVisible.value = false;
      }
    });
  }
};

// Watch for map ready state
watch(() => props.mapReady, (newValue) => {
  if (newValue) {
    initializeLayer();
  }
});

// Clean up on component unmount
onUnmounted(() => {
  if (props.mapInstance && anchorageLayer.value) {
    props.mapInstance.off('moveend', updateClusters);
    props.mapInstance.off('zoomend', updateClusters);
    props.mapInstance.removeLayer(anchorageLayer.value);
  }
});
</script>

<style>
.custom-cluster-icon {
  background: none;
  border: none;
}

.cluster-marker {
  background-color: #48506a;
  border-radius: 50%;
  border: 2px solid #666;
  color: #ffff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.cluster-marker.small {
  width: 30px;
  height: 30px;
  font-size: 12px;
}

.cluster-marker.medium {
  width: 35px;
  height: 35px;
  font-size: 14px;
}

.cluster-marker.large {
  width: 40px;
  height: 40px;
  font-size: 16px;
}

.text-green-500 {
  color: green;
}

.text-red-500 {
  color: red;
}
</style>