<template>
  <div class="sidebar-container">
    <!-- Fixed header section -->
    <div class="sidebar-header">
      <VueMultiselect
  v-model="selectedCables"
  track-by="id"
  label="name"
  :options="landingSitesAndCables"
  :searchable="true"
  :loading="isLoading"
  :internal-search="false"
  :show-no-options="false"
:placeholder="selectedCables && selectedCables.length > 0 ? '' : 'Search...'"
  @search-change="asyncFind"
  @select="selectCableOrLandingSiteOrVessel"
>

  <!-- Custom option template (keep your existing template) -->
  <template v-slot:option="{ option }">
    <div class="d-flex align-items-center">
      <!-- Cable icon -->
      <div v-if="option.type === 'cable'" class="cable-icon-container me-2">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24" class="landing-icon">
          <path d="M4 8c4 8 12-8 16 8" stroke="white" stroke-width="2" stroke-linecap="round"/>
          <circle cx="4" cy="8" r="2" :fill="getLandingColor(option)" stroke="white" stroke-width="1"/>
          <circle cx="20" cy="16" r="2" :fill="getLandingColor(option)" stroke="white" stroke-width="1"/>
        </svg>
      </div>
      <!-- Landing site icon -->
      <div v-if="option.type === 'landingSite'" class="landing-icon-container me-2">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24" class="landing-icon">
          <path d="M12 2C8.69 2 6 4.69 6 8c0 2.64 2.07 6.64 4.42 9.71 0 .01.01.02.02.03l1.28 1.56 1.28-1.56c.01-.01.02-.02.02-.03C15.93 14.64 18 10.64 18 8c0-3.31-2.69-6-6-6zm0 2c2.21 0 4 1.79 4 4 0 1.91-1.63 5.17-4 8.33-2.37-3.16-4-6.42-4-8.33 0-2.21 1.79-4 4-4zm-1 3v2h2V7h-2zm0 4v2h2v-2h-2zm-9 6v2h20v-2h-20z"
            :fill="getLandingColor(option)" stroke="white" stroke-width="1"/>
        </svg>
      </div>
      <!-- Ship icon -->
      <div v-if="option.type === 'vessel'" class="ship-icon-container me-2">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24" class="ship-icon">
          <path d="M20 21c-1.39 0-2.78-.47-4-1.32-2.44 1.71-5.56 1.71-8 0C6.78 20.53 5.39 21 4 21H2v2h2c1.38 0 2.74-.35 4-.99 2.52 1.29 5.48 1.29 8 0 1.26.65 2.62.99 4 .99h2v-2h-2zM3.95 19H4c1.6 0 3.02-.88 4-2 .98 1.12 2.4 2 4 2s3.02-.88 4-2c.98 1.12 2.4 2 4 2h.05l1.89-6.68c.08-.26.06-.54-.06-.78s-.34-.42-.6-.5L20 10.62V6c0-1.1-.9-2-2-2h-3V1H9v3H6c-1.1 0-2 .9-2 2v4.62l-1.29.42c-.26.08-.48.26-.6.5s-.15.52-.06.78L3.95 19zM6 6h12v3.97L12 8 6 9.97V6z"
            :fill="getShipColor(option)" stroke="white" stroke-width="1"/>
        </svg>
        <span class="ship-flag">{{ getFlagEmoji(option) }}</span>
      </div>
      <span>{{ option.name }}</span>
    </div>
  </template>
</VueMultiselect>

      <VesselTracking
        ref="vesselTrackingRef"
        :mapInstance="mapInstance"
        :currentLanguage="currentLanguage"
        :cableStore="cableStore"
        :controlInstance="controlInstance"
        :layerGroup="layerGroup"
        :mapReady="mapReady"
        :vesselPositions="vesselPositions"
        @update:vesselPositions="updateVesselPositions"
        @update:hoveredMarker="updateHoveredMarker"
        @update:viewingVesselTrack="updateViewingVesselTrack"
      />
    </div>

    <!-- Single scrollable container for both content and footer -->
    <div class="scrollable-container">
      <div class="scrollable-content">
        <router-view name="sidebar" />
      </div>
      
      <!-- Footer at the bottom of scrollable content -->
      <div class="sidebar-footer">
        <img src="@/assets/logo.png" :style="{ width: '250px', height: 'auto' }" />
        <SourcesModal />
        <p>
          Copyright  Jonas Franken, 2024<br />
          Licensed under the
          <a target="blank" href="https://joinup.ec.europa.eu/collection/eupl/eupl-text-eupl-12">EUPL</a><br />
          <a href="mailto:franken@peasec.de">franken@peasec.de</a>
        </p>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, watch, onMounted, nextTick } from 'vue';
import L from 'leaflet';
import VueMultiselect from 'vue-multiselect';
import { useDataCablesStore } from '@/store/datacables';
import { useLandingSitesStore } from '@/store/landingSites';
import SourcesModal from './SourcesModal.vue';
import vesselIds from '@/assets/vesselIds.json';
import VesselTracking from '@/components/VesselTracking.vue';
import axios from 'axios';

const props = defineProps({
  currentLanguage: { type: String, required: true },
  cableStore: { type: Object, required: true },
  controlInstance: { type: Object, required: true },
  layerGroup: { type: Object, required: true },
  mapReady: { type: Boolean, required: true },
  vesselPositions: { type: Array, default: () => [] }
});

const emit = defineEmits(['update:vesselPositions', 'update:hoveredMarker', 'update:viewingVesselTrack']);

const vesselTrackingRef = ref(null);
const cableStore = useDataCablesStore();

const landingSiteStore = useLandingSitesStore();
const selectedCables = ref([]);
const selectedLandingSite = ref(null);
const landingSitesAndCables = ref([]);
const isLoading = ref(false);
let inputArray = [];
const mapInstance = ref(null);
const hoveredMarker = ref(null);

const sidebarMarkerLayerGroup = ref<L.LayerGroup | null>(null);

// Helper function to get ship color
function getShipColor(vessel) {
  return vessel.color || '#636363'; // Default color if none is provided
}

// Helper function to get flag emoji
function getFlagEmoji(vessel) {
  return vessel.flag || ''; // Default flag emoji if none is provided
}

// Helper function to get cable color
function getCableColor(cable) {
  return cable.color || '#007BFF'; // Default color for cables
}

// Helper function to get landing site color
function getLandingColor(landingSite) {
  return landingSite.color || '#28A745'; // Default color for landing sites
}

const onShipClickMethod = ref<((event: any, vessel: any) => void) | null>(null);
const currentSidebarMarker = ref(null);

// Watch for hovered marker updates
watch(() => hoveredMarker.value, (newMarker) => {
  if (newMarker && mapInstance.value) {
    newMarker.addTo(mapInstance.value);
  } else if (hoveredMarker.value && mapInstance.value && newMarker === null) {
    mapInstance.value.removeLayer(hoveredMarker.value);
    hoveredMarker.value = null;
  }
}, { deep: true });

const updateHoveredMarker = (marker) => {
  if (hoveredMarker.value && mapInstance.value && mapInstance.value.hasLayer(hoveredMarker.value)) {
    mapInstance.value.removeLayer(hoveredMarker.value);
  }
  hoveredMarker.value = marker;
};

const updateVesselPositions = (positions) => {
  emit('update:vesselPositions', positions);
};

const updateViewingVesselTrack = (isViewing) => {
  emit('update:viewingVesselTrack', isViewing);
};

// Function to clear sidebar vessel
function clearSidebarVessel() {
  if (sidebarMarkerLayerGroup.value) {
    // Clear all markers from the layer group
    sidebarMarkerLayerGroup.value.clearLayers();
  }

  // Update the vesselPositions array to exclude the sidebar-created vessel
  if (props.vesselPositions && props.vesselPositions.length > 0) {
    const updatedPositions = props.vesselPositions.filter(vessel => !vessel.fromSidebar);
    emit('update:vesselPositions', updatedPositions);
  }
}

async function handleVesselSearchSelect(vessel) {
  // Clear any existing sidebar vessel first
  clearSidebarVessel();

  if (!vesselTrackingRef.value) {
    console.error('VesselTracking component not initialized');
    return;
  }

  try {
    const vesselInfo = vessel;
    const latestPosition = await getVesselLatestPosition(vesselInfo.vesselId);

    if (!latestPosition) {
      console.error('Could not get latest position for vessel:', vesselInfo.vesselId);
      return;
    }

    const vesselData = {
      ...vessel,
      ...latestPosition.vessel,
      id: vesselInfo.vesselId,
      position: latestPosition.position,
      lastSeen: latestPosition.lastSeen || new Date().toISOString(),
      eventType: latestPosition.eventType || 'Unknown',
      flag: latestPosition.vessel?.flag || vessel.country || 'Unknown',
      ssvid: vessel.ssvid || latestPosition.vessel?.ssvid,
      name: vessel.name || latestPosition.vessel?.name || 'Unknown Vessel',
      length: vesselInfo.length || 'Unknown',
      beam: vesselInfo.beam || 'Unknown',
      grossTonage: vesselInfo.grossTonage || 'Unknown',
      purpose: vesselInfo.purpose || 'Unknown',
      cableCapacity: vesselInfo.cableCapacity || 'Unknown',
      notes: vesselInfo.notes || 'Unknown',
      baseport: vesselInfo.baseport || 'Unknown',
      owner: vesselInfo.owner || 'Unknown',
      fromSidebar: true,
    };

    // Update parent component's vessel positions
    if (Array.isArray(props.vesselPositions)) {
      const updatedPositions = [...props.vesselPositions, vesselData];
      emit('update:vesselPositions', updatedPositions);
    } else {
      emit('update:vesselPositions', [vesselData]);
    }

    cableStore.setAllCablesOpacity(0.15);

    // Center map on vessel position
    if (mapInstance.value && vesselData.position) {
      mapInstance.value.setView([vesselData.position.lat, vesselData.position.lon], 8);
    }

    await nextTick();

    if (!vesselTrackingRef.value) {
      console.error('VesselTracking component not initialized');
      return;
    }

    // Create and add marker using Leaflet
    const shipIcon = vesselTrackingRef.value.createShipIcon('#636363', vesselData.flag, vesselData);

    if (mapInstance.value && vesselData.position) {
      const marker = new L.Marker([vesselData.position.lat, vesselData.position.lon], {
        icon: shipIcon,
      });

      // Add click handler to the marker
      marker.on('click', (e) => {
        const clickEvent = {
          latlng: {
            lat: vesselData.position.lat,
            lng: vesselData.position.lon,
          },
          originalEvent: e.originalEvent,
        };

        if (typeof vesselTrackingRef.value.onShipClick === 'function') {
          vesselTrackingRef.value.onShipClick(clickEvent, vesselData);
        }
      });

      // Add the marker to the sidebar marker layer group
      if (sidebarMarkerLayerGroup.value) {
        marker.addTo(sidebarMarkerLayerGroup.value);
      }

      // Store the marker reference for later removal
      vesselData.marker = marker;
      currentSidebarMarker.value = marker;
      marker.fromSidebar = true; // Mark the marker as created from sidebar
    }

    // Trigger the initial popup with vessel information
    const mockEvent = {
      latlng: {
        lat: vesselData.position.lat,
        lng: vesselData.position.lon,
      },
      originalEvent: new Event('click'),
    };

    // Show the initial popup with vessel information
    if (typeof vesselTrackingRef.value.onShipClick === 'function') {
      vesselTrackingRef.value.onShipClick(mockEvent, vesselData);
    }
  } catch (error) {
    console.error('Error in handleVesselSearchSelect:', error);
  }
}

// Whle Kabel aus Liste aus, sobald eines selektiert wird
watch(() => cableStore.selectCable, (state) => {
  if (state.length > 0) {
    const selectedCable = inputArray.find((str) => state[0] == str.id);
    selectedCables.value = selectedCable;
  } else {
    selectedCables.value = [];
  }
  
  if (selectedLandingSite.value?.geoData?.length > 0) {
    selectedCables.value = selectedLandingSite.value;
  }
});

watch(() => landingSiteStore.selectLandingSites, (selectedLandingSites) => {
  if (selectedLandingSites.length > 0) {
    selectedLandingSite.value = inputArray.find((str) => selectedLandingSites[0].id == str.id);
  } else {
    selectedLandingSite.value = null;
    selectedCables.value = [];
  }
});

// Watcher for selectedCables
watch(() => selectedCables.value, (newValue, oldValue) => {
  if (!newValue || newValue.length === 0) {
    clearSidebarVessel();
  }
});

async function getVesselLatestPosition(vesselId) {
  const vesselTrackingUrl = 'https://sdcs-api.dev.peasec.de/fishing/v3/events';
  const params = {
    limit: 1,
    offset: 0,
    sort: '-start',
    vessels: [vesselId]
  };
  
  try {
    // Try both loitering and port visit events
    params['datasets[0]'] = 'public-global-loitering-events:latest';
    const loiteringResponse = await axios.get(vesselTrackingUrl, { params });
    
    params['datasets[0]'] = 'public-global-port-visits-c2-events:latest';
    const portVisitResponse = await axios.get(vesselTrackingUrl, { params });
    
    let latestEvent = null;
    let eventType = '';
    
    // Get the latest event by comparing dates
    if (loiteringResponse.data.entries && loiteringResponse.data.entries.length > 0 &&
        portVisitResponse.data.entries && portVisitResponse.data.entries.length > 0) {
      // If we have both types of events, compare their dates
      const loiteringEvent = loiteringResponse.data.entries[0];
      const portVisitEvent = portVisitResponse.data.entries[0];
      
      const loiteringDate = new Date(loiteringEvent.end);
      const portVisitDate = new Date(portVisitEvent.end);
      
      if (loiteringDate > portVisitDate) {
        latestEvent = loiteringEvent;
        eventType = 'Loitering';
      } else {
        latestEvent = portVisitEvent;
        eventType = 'PortVisit';
      }
    } else if (loiteringResponse.data.entries && loiteringResponse.data.entries.length > 0) {
      // Only loitering events available
      latestEvent = loiteringResponse.data.entries[0];
      eventType = 'Loitering';
    } else if (portVisitResponse.data.entries && portVisitResponse.data.entries.length > 0) {
      // Only port visit events available
      latestEvent = portVisitResponse.data.entries[0];
      eventType = 'PortVisit';
    }
        
    if (latestEvent) {
      return {
        position: latestEvent.position,
        lastSeen: latestEvent.end,
        ssvid: latestEvent.vessel?.ssvid || 'Unknown',
        eventType: eventType,
        vessel: {
          ...latestEvent.vessel,
          name: latestEvent.vessel.name || 'Unknown Vessel'
        }
      };
    }
    
    return null;
  } catch (error) {
    console.error("Error fetching vessel position:", error);
    return null;
  }
}

async function initializeSearchBar() {
  await cableStore.initialize();
  const vesselSearchOptions = Object.values(vesselIds).map((vessel) => ({
    id: vessel.vesselId,
    name: vessel.shipname,
    type: 'vessel', // Add type for vessels
    ssvid: vessel.ssvid,
    color: '#636363', // Default color
    flag: vessel.flag || '', // Use vessel's flag or default
    ...vessel,
  }));

  const cableOptions = cableStore.getDataCables.map((cable) => ({
    ...cable,
    type: 'cable', // Add type for cables
  }));

  const landingSiteOptions = landingSiteStore.getLandingSites.map((landingSite) => ({
    ...landingSite,
    type: 'landingSite', // Add type for landing sites
  }));

  // Combine all options
  inputArray = cableOptions.concat(landingSiteOptions).concat(vesselSearchOptions);
  landingSitesAndCables.value = inputArray;
}

onMounted(async () => {
  await initializeSearchBar();
  // Set map instance with validation
  mapInstance.value = cableStore.getMapInstance;
  
  // Only create the layer group if the map instance exists and is ready
  if (mapInstance.value && mapInstance.value._loaded) {
    sidebarMarkerLayerGroup.value = L.layerGroup().addTo(mapInstance.value);
  } else {
    console.warn("Map instance not ready during component mount, will initialize layer group later");
    
    // Set up a function to retry creating the layer group
    const initLayerGroup = () => {
      if (mapInstance.value && mapInstance.value._loaded) {
        sidebarMarkerLayerGroup.value = L.layerGroup().addTo(mapInstance.value);
      } else {
        setTimeout(initLayerGroup, 200); // Retry after a delay
      }
    };
    
    // Start the retry process
    setTimeout(initLayerGroup, 200);
  }
  
  // Initialize landingSitesAndCables with all options
  landingSitesAndCables.value = inputArray;
});

// find cable in list function
async function asyncFind(queryGiven: string) {
  // Debugging: Log the current search term

  // Clear the sidebar-created ship icon and marker
  clearSidebarVessel();

  // If the search term is empty, show all options
  if (!queryGiven) {
    landingSitesAndCables.value = inputArray;
    return;
  }

  // Perform the search if a query is provided
  const regex = new RegExp(queryGiven.replace(/[-[]{}()+?.,\^$|#\s]/g, '&'), 'i');
  const filteredArray = inputArray.filter((item) => {
    if (item && typeof item.name === 'string') {
      return regex.test(item.name.replace(/[-[]{}()+?.,\^$|#\s]/g, ''));
    }
    return false;
  });

  landingSitesAndCables.value = filteredArray;
}

function selectCableOrLandingSiteOrVessel(selection) {
  if (selection.type === 'vessel') {
    handleVesselSearchSelect(selection);
  } else if (selection.country) {
    cableStore.deselectCables();
    landingSiteStore.setSelectLandingSite(selection, true);
  } else {
    landingSiteStore.deselectLandingSite();
    cableStore.setSelectCable(selection.id, true);
  }
}
</script>

