<template>
  <div v-if="isVisible" class="anchorage-layer-toggle-popup">
    <h3 class="anchorage-layer-title">{{ currentTranslations.anchorageLayerToggles }}</h3>
    <div class="layers-container">
      <div v-for="layer in layers" :key="layer.name" class="layer-toggle">
        <input 
          :id="layer.name" 
          type="checkbox" 
          :checked="layer.visible" 
          @change="toggleLayer(layer)" 
          class="layer-checkbox"
        />
        <label :for="layer.name" class="layer-label">
          {{ layer.translatedName }}
        </label>
      </div>
    </div>
  </div>
</template>


<script setup>
import { ref, watch, onMounted } from 'vue';

const props = defineProps({
  mapInstance: { type: Object, required: true },
  controlInstance: { type: Object, required: true },
  currentTranslations: { type: Object, required: true }
});

const isVisible = ref(false);

// Define the layers with predefined names to match
const layerDefinitions = [
  { name: 'Seamarks', translatedKey: 'seamarksLayer', defaultName: 'Seamarks' },
  { name: 'FixedInfrastructure', translatedKey: 'fixedInfrastructureLayer', defaultName: 'Fixed Infrastructure' },
  { name: 'Anchorage', translatedKey: 'anchorageLayer', defaultName: 'Anchorage' }
];

// Reactive layers array
const layers = ref(layerDefinitions.map(def => ({
  name: def.name,
  translatedName: props.currentTranslations[def.translatedKey] || def.defaultName,
  layer: null,
  visible: false
})));

// Initialize layers when map is ready
const initializeLayers = () => {
  if (!props.controlInstance) return;

  // Find the grouped layer
  const groupedLayerObj = props.controlInstance._layers.find(
    layerObj => layerObj.originalName === "Anchorage and seabed use"
  );

  if (!groupedLayerObj) {
    console.warn("Grouped layer 'Anchorage and seabed use' not found");
    return;
  }

  const groupedLayer = groupedLayerObj.layer;

  // Get all layers in the group
  const allGroupLayers = groupedLayer.getLayers();

  // Match layers dynamically
  layers.value.forEach((layerConfig, index) => {
    // Try to match layer by name similarity or use the layer by index
    const matchedLayer = allGroupLayers.find(layer => 
      layer.options?.name?.toLowerCase().includes(layerConfig.name.toLowerCase())
    ) || allGroupLayers[index];

    if (matchedLayer) {
      layerConfig.layer = matchedLayer;
      
      // Check if the layer is currently on the map
      layerConfig.visible = props.mapInstance.hasLayer(matchedLayer);
      
      // If layer should be visible but isn't, add it
      if (layerConfig.visible) {
        props.mapInstance.addLayer(matchedLayer);
      }
    }
  });
};

// Toggle individual layer visibility
const toggleLayer = (layerConfig) => {
  if (!layerConfig.layer) return;

  layerConfig.visible = !layerConfig.visible;
  
  if (layerConfig.visible) {
    props.mapInstance.addLayer(layerConfig.layer);
  } else {
    props.mapInstance.removeLayer(layerConfig.layer);
  }
};

// Watch for changes in control instance
watch(() => props.controlInstance, () => {
  initializeLayers();
}, { immediate: true });

defineExpose({
  show: () => {
    isVisible.value = true;
    initializeLayers();
  },
  hide: () => {
    isVisible.value = false;
  }
});
</script>

