            </div>
          </div>
        </div>
      </div>

      <!-- Landing Sites Card -->
      <div v-if="cable.CableSystem_X_Landingsites?.length" class="info-card">
        <div class="card-header">
          <i class="bi bi-geo-alt"></i>
