<template>
  <div>
    <div v-if="drawControlsInitialized" class="drawing-controls">
      <div class="button-container">
        <button 
          @click="toggleDrawingMode(false)" 
          class="toggle-drawing-btn" 
          :data-drawing-active="isDrawingModeActive"
        >
          <img :src="require('@/assets/graphics/seadepth.png')" alt="Drawing Toggle" class="drawing-icon"/>
        </button>
      </div>
      
      <!-- Updated Sample Points Dropdown -->
      <div v-if="isDrawingModeActive" class="sample-points-control">
        <div class="dropdown-wrapper">
          <div class="dropdown-label">Analysis Detail</div>
          <select 
            id="samplePointCount" 
            v-model="samplePointCount" 
            class="dropdown"
          >
            <option value="100">Quick Scan</option>
            <option value="500">Standard Detail</option>
            <option value="1000">High Detail</option>
            <option value="2000">Very High Detail</option>
            <option value="5000">Ultra Detailed</option>
          </select>
        </div>
      </div>
    </div>
    
    <OceanDepthAnalyzer 
      v-if="currentGeometry"
      :map-instance="props.mapInstance"
      :drawn-geometry="currentGeometry"
      :sample-point-count="samplePointCount"
      :showPopup="true"
    />
  </div>
</template>
  
  <script setup lang="ts">
  import { ref, onMounted, onUnmounted, watch } from 'vue';
import L from 'leaflet';
import 'leaflet-draw';
import 'leaflet-draw/dist/leaflet.draw.css';
import "leaflet-draw/dist/leaflet.draw-src.css";
import OceanDepthAnalyzer from './OceanDepthAnalyzer.vue';

// Props for receiving map instance
const props = defineProps({
  mapInstance: {
    type: Object,
    required: true
  },
  oceanSelected: {
    type: Boolean,
    required: true
  }
});

const currentGeometry = ref<L.Layer | null>(null);
const drawControl = ref(null);
const drawnItemsLayer = ref(null);
const lastGeometry = ref<L.Layer | null>(null);
const drawnItems = ref([]);
const isDrawingModeActive = ref(false);
const isEditModeActive = ref(false);
const drawControlsInitialized = ref(false);
const samplePointCount = ref(100);

// Initialize drawing layer
onMounted(() => {
  if (props.mapInstance) {
    initializeDrawingControls();
  }
});

// Cleanup when component is unmounted
onUnmounted(() => {
  if (props.mapInstance) {
    removeMapEventListeners();
    if (drawnItemsLayer.value) {
      props.mapInstance.removeLayer(drawnItemsLayer.value);
    }
    if (lastGeometry.value) {
      props.mapInstance.removeLayer(lastGeometry.value);
    }
  }
});

function removeMapEventListeners() {
  props.mapInstance.off(L.Draw.Event.CREATED);
  props.mapInstance.off(L.Draw.Event.DELETED);
  props.mapInstance.off(L.Draw.Event.DRAWSTART);
  props.mapInstance.off(L.Draw.Event.DELETESTART);
  props.mapInstance.off(L.Draw.Event.DELETESTOP);
  props.mapInstance.off(L.Draw.Event.EDITSTART);
  props.mapInstance.off(L.Draw.Event.EDITSTOP);
}

watch(() => props.mapInstance, (newMap) => {
  if (newMap) {
    initializeDrawingControls();
  }
});

watch(() => props.oceanSelected, (newValue) => {
  if (!newValue) {
    toggleDrawingMode(true);
  }
});

function initializeDrawingControls() {
  // Create a layer group for drawn items
  drawnItemsLayer.value = new L.FeatureGroup();
  props.mapInstance.addLayer(drawnItemsLayer.value);

  // Configure drawing options
  const drawOptions = {
    draw: {
      polyline: {
        shapeOptions: { color: '#ff0000' },
        metric: false,
        nautic: true,
        feet: false
      },
      polygon: {
        shapeOptions: { color: '#ff0000' },
        metric: false,
        nautic: true,
        feet: false,
        showArea: true
      },
      rectangle: {
        shapeOptions: { color: '#ff0000' },
        metric: false,
        nautic: true,
        showArea: true
      },
      circle: {
        shapeOptions: { color: '#ff0000' },
        metric: false,
        nautic: true,
        showRadius: true
      },
      marker: {},
      circlemarker: false,
    },
    edit: {
      featureGroup: drawnItemsLayer.value,
      remove: true
    },
    position: 'topleft',
    className: 'custom-draw-toolbar'
  };

  // Create drawing control
  drawControl.value = new L.Control.Draw(drawOptions);

  // Event handlers for drawing
  props.mapInstance.on(L.Draw.Event.CREATED, handleDrawCreated);
  props.mapInstance.on(L.Draw.Event.DELETED, handleDrawDeleted);
  props.mapInstance.on(L.Draw.Event.DRAWSTART, handleDrawStart);
  props.mapInstance.on(L.Draw.Event.DELETESTART, handleDeleteStart);
  props.mapInstance.on(L.Draw.Event.DELETESTOP, handleDeleteStop);
  props.mapInstance.on(L.Draw.Event.EDITSTART, handleEditStart);
  props.mapInstance.on(L.Draw.Event.EDITSTOP, handleEditStop);
  
  drawControlsInitialized.value = true;
}

function handleDeleteStart() {
  isEditModeActive.value = true;
  // Remove click handlers during delete mode
  if (drawnItemsLayer.value) {
    drawnItemsLayer.value.eachLayer((layer: L.Layer) => {
      layer.off('click');
    });
  }
  if (lastGeometry.value) {
    lastGeometry.value.off('click');
  }
}

function handleDeleteStop() {
  isEditModeActive.value = false;
  // Restore click handlers
  if (drawnItemsLayer.value) {
    drawnItemsLayer.value.eachLayer((layer: L.Layer) => {
      setupClickHandler(layer);
    });
  }
  if (lastGeometry.value && currentGeometry.value) {
    setupClickHandler(lastGeometry.value, currentGeometry.value);
  }
}

function handleEditStart() {
  isEditModeActive.value = true;
  // Remove last geometry highlight during edit mode
  if (lastGeometry.value) {
    props.mapInstance.removeLayer(lastGeometry.value);
    lastGeometry.value = null;
  }
}

function handleEditStop() {
  isEditModeActive.value = false;
  // Restore highlight if needed
  if (currentGeometry.value) {
    const highlightedLayer = createHighlightedLayer(currentGeometry.value);
    if (highlightedLayer) {
      lastGeometry.value = highlightedLayer;
      props.mapInstance.addLayer(highlightedLayer);
      setupClickHandler(highlightedLayer, currentGeometry.value);
    }
  }
}

function setupClickHandler(layer: L.Layer, targetLayer: L.Layer | null = null) {
  if (!isEditModeActive.value) {
    layer.on('click', (event) => {
      if (!isEditModeActive.value) {
        L.DomEvent.stopPropagation(event);
        (targetLayer || layer).fire('click', event);
      }
    });
  }
}

function toggleDrawingMode(fromWatcher) {
  if (!fromWatcher) isDrawingModeActive.value = !isDrawingModeActive.value;
  
  if (isDrawingModeActive.value && !fromWatcher) {
    props.mapInstance.addControl(drawControl.value);
  } else if (!props.oceanSelected && isDrawingModeActive.value) {
    cleanupGeometries();
    props.mapInstance.removeControl(drawControl.value);
    isDrawingModeActive.value = !isDrawingModeActive.value;
  } else {
    cleanupGeometries();
    props.mapInstance.removeControl(drawControl.value);
  }
}

function cleanupGeometries() {
  currentGeometry.value = null;
  if (drawnItemsLayer.value) {
    drawnItemsLayer.value.clearLayers();
  }
  drawnItems.value = [];
  if (lastGeometry.value) {
    props.mapInstance.removeLayer(lastGeometry.value);
    lastGeometry.value = null;
  }
}

function handleDrawStart() {
  if (lastGeometry.value) {
    props.mapInstance.removeLayer(lastGeometry.value);
    lastGeometry.value = null;
  }
}

function handleDrawCreated(e) {
  const layer = e.layer;
  
  setupClickHandler(layer);
  drawnItemsLayer.value.addLayer(layer);
  currentGeometry.value = layer;
  
  const highlightedLayer = createHighlightedLayer(layer);
  if (highlightedLayer) {
    if (lastGeometry.value) {
      props.mapInstance.removeLayer(lastGeometry.value);
    }
    
    lastGeometry.value = highlightedLayer;
    setupClickHandler(highlightedLayer, layer);
    drawnItemsLayer.value.addLayer(highlightedLayer);
  }

  const itemDetails = {
    type: e.layerType,
    coordinates: getLayerCoordinates(layer)
  };
  drawnItems.value.push(itemDetails);
}

function createHighlightedLayer(layer: L.Layer): L.Layer | null {
  const highlightStyles = {
    color: '#00ff00',
    weight: 3,
    opacity: 1,
    fillColor: '#00ff00',
    fillOpacity: 0.3
  };

  if (layer instanceof L.Marker) {
    return L.marker(layer.getLatLng(), {
      icon: L.divIcon({
        className: 'last-geometry-marker',
        html: '<div style="background-color:#00ff00;width:20px;height:20px;border-radius:50%;border:2px solid #00ff00;"></div>',
        iconSize: [20, 20]
      })
    });
  } else if (layer instanceof L.Polygon || layer instanceof L.Rectangle) {
    const coords = layer.getLatLngs();
    return L.polygon(coords, { ...highlightStyles, smoothFactor: 1, noClip: true });
  } else if (layer instanceof L.Polyline) {
    return L.polyline(layer.getLatLngs(), highlightStyles);
  } else if (layer instanceof L.Circle) {
    return L.circle(layer.getLatLng(), {
      ...highlightStyles,
      radius: layer.getRadius()
    });
  }
  return null;
}

function handleDrawDeleted(e) {
  const layers = e.layers;
  
  layers.eachLayer((layer) => {
    const index = drawnItems.value.findIndex(item => 
      JSON.stringify(getLayerCoordinates(layer)) === JSON.stringify(item.coordinates)
    );
    if (index !== -1) {
      drawnItems.value.splice(index, 1);
    }
    
    if (currentGeometry.value === layer) {
      currentGeometry.value = null;
      if (lastGeometry.value) {
        drawnItemsLayer.value.removeLayer(lastGeometry.value);
        lastGeometry.value = null;
      }
    }
  });
}

function getLayerCoordinates(layer: L.Layer | any) {
  try {
    if (layer instanceof L.Marker) {
      return layer.getLatLng();
    } else if (layer instanceof L.Polyline) {
      return layer.getLatLngs();
    } else if (layer instanceof L.Rectangle || layer instanceof L.Polygon) {
      return layer.getBounds();
    } else if (layer instanceof L.Circle) {
      return {
        center: layer.getLatLng(),
        radius: layer.getRadius()
      };
    }
    return null;
  } catch (error) {
    console.error('Error getting layer coordinates:', error);
    return null;
  }
}

defineExpose({
  isDrawingModeActive,
  samplePointCount
});
  </script>
  
