<template>
  <!-- Consent modal -->
  <VueFinalModal v-model="showConsent" :clickToClose="false" :styles="modalStyles">
    <div class="modal-overlay"></div>
    <div class="modal-content">
      <!-- Show both languages -->
      <div class="language-section">
        <h2>Einverstndniserklrung zur Aufzeichnung</h2>
        <p>
          Im Rahmen einer Studie der TU Darmstadt wird dieses Dashboard evaluiert.
          Dabei werden Ihre anonymisierten Interaktionen auf der Webseite mit dem Open-Source-Tool OpenReplay" aufgezeichnet, 
          um die Benutzbarkeit zu verbessern. Es werden keine IP-Adressen, Zeitstempel oder persnliche Daten gespeichert.
          Sie knnen die Teilnahme jederzeit abbrechen. In diesem Fall werden alle erhobenen Daten (auch LimeSurvey und OpenReplay) gelscht.
          Ihre Einwilligung ist freiwillig und Sie knnen selbst entscheiden, ob Sie teilnehmen mchten.
        </p>
      </div>

      <div class="language-section">
        <h2>Consent for Session Recording</h2>
        <p>
          As part of a study conducted by TU Darmstadt, this dashboard is being evaluated.
          Your anonymized interactions on the site may be recorded using the open-source tool "OpenReplay" to help improve usability.
          No IP addresses, timestamps, or personal data are stored. You may withdraw at any time and all collected data 
          (including LimeSurvey and OpenReplay) will be deleted. Your participation is voluntary and you decide whether to consent.
        </p>
      </div>

      <div class="button-container">
        <button class="consent-button" @click="acceptTracking">
          Einverstanden & Fortfahren / Accept & Continue
        </button>
        <button class="consent-button" @click="declineTracking">
          Ablehnen / Decline
        </button>
      </div>
    </div>
  </VueFinalModal>

  <!-- Existing functionality overview modal -->
  <VueFinalModal v-model="show" :clickToClose="false" :styles="modalStyles">
    <div class="modal-overlay" @click="closeModal"></div>
    <div class="modal-content">
      <h2>Welcome to an overview on the SDCS Dashboard!</h2>
      <p>Below is a short summary of its core functionalities:</p>
      <img src="../assets/functionality-overview.jpg" class="screenshot">
      <ul class="functionality-list">
        <li><span class="step-number">1</span> Click on <strong>Cable</strong> to view details</li>
        <li><span class="step-number">2</span> Click on <strong>Landing Site</strong></li>
        <li><span class="step-number">3</span> Switch <strong>Layer / Overlay</strong></li>
        <li><span class="step-number">4</span> Use the <strong>Distance Measuring Tool</strong></li>
        <li><span class="step-number">5</span> Change language, source, or reopen overview</li>
      </ul>
      <button @click="closeModal" class="close-button">Close</button>
    </div>
  </VueFinalModal>
</template>

<script>
import { ref, onMounted, provide } from 'vue';
import { VueFinalModal } from 'vue-final-modal';
import Tracker from '@openreplay/tracker';

export default {
  components: { VueFinalModal },
  setup() {
    const show = ref(false);
    const showConsent = ref(false);
    const hasConsented = ref(false);
    const tracker = ref(null);

    const initializeTracker = () => {
 tracker.value = new Tracker({
  projectKey: 'eMRBZNonYJms799KwZqT',
  ingestPoint: "https://openreplay.dev.peasec.de/ingest",
  privateMode: false,
  canvas: { disableCanvas: false, useAnimationFrame: true, fileExt: 'webp' },
  resourceBaseHref: 'https://ubiquitous-hummingbird-229317.netlify.app',
});

      
      // Make it available to child components
      provide('openreplay-tracker', tracker.value);
      // Also keep it globally available for simplicity
      window._trackerInstance = tracker.value;
    };


    const startTracker = () => {
      if (tracker.value && !tracker.value.isActive()) {
        try {
          tracker.value.start();
          console.log('[OpenReplay] Tracker started after consent');
        } catch (error) {
          console.error('[OpenReplay] Failed to start tracker:', error);
        }
      }
    };

    onMounted(() => {
      initializeTracker();
      
      // Check for existing consent
      const consent = localStorage.getItem('openreplay-consent');
      if (consent === 'true') {
        hasConsented.value = true;
        startTracker();
        show.value = true;
      } else {
        // Always show consent modal if not previously accepted
        showConsent.value = true;
      }
    });

    const acceptTracking = () => {
      localStorage.setItem('openreplay-consent', 'true');
      hasConsented.value = true;
      showConsent.value = false;
      startTracker();
      show.value = true;
    };

    const declineTracking = () => {
      localStorage.setItem('openreplay-consent', 'false');
      hasConsented.value = false;
      showConsent.value = false;
      show.value = true;
    };

    const closeModal = () => {
      show.value = false;
    };

    // Method to reset consent (useful for testing)
    const resetConsent = () => {
      localStorage.removeItem('openreplay-consent');
      hasConsented.value = false;
      showConsent.value = true;
      show.value = false;
      if (tracker.value && tracker.value.isActive()) {
        tracker.value.stop();
      }
    };

    const modalStyles = {
      overlay: {
        zIndex: 1000,
      },
      content: {
        zIndex: 1001,
      },
    };

    return {
      show,
      showConsent,
      hasConsented,
      acceptTracking,
      declineTracking,
      closeModal,
      resetConsent,
      modalStyles
    };
  },
};
</script>

